import React from 'react';
import PropTypes from "prop-types";
import {ImSpinner3} from "react-icons/all";
import "styles/keys-page/UnlockPopup.css";
import "styles/ToplButtons.css";
import {CloseButton} from "../CloseButton";
import {createAndSendRequest} from "modules/apiRequests";

/**
 * Props for {@link UnlockPopup}
 *  @property {string} keyfileName Name of keyfile that is being unlocked
 *  @property {string} address Address of this keyfile to unlock
 *  @property {function} toggle To close the pop-up
 *  @property {function} updateData For updating data (retrieving data from backend)
 *  @property {function} lockKeyfile For locking keyfile
 *  @property {string} currentUnlocked Currently unlocked keyfile
 */
const unlockPopupPropTypes = {
    keyfileName: PropTypes.string.isRequired,
    address: PropTypes.string.isRequired,
    toggle: PropTypes.func.isRequired,
    updateData: PropTypes.func.isRequired,
    lockKeyfile: PropTypes.func.isRequired,
    currentUnlocked: PropTypes.string
}

/** Unlock popup - displayed when a user clicks to unlock a keyfile in {@link KeyCard} */
export class UnlockPopup extends React.Component {
    constructor(props) {
        super(props);
        this.state={
            password: "",
            showWarning: false,
            loading: false,
            errorMsg: ""
        }
        this.handleChange = this.handleChange.bind(this);
    }

    /**
     * Function to handle change to "password" input field.
     * @param event - the event that triggers the onChange function call.
     */
    handleChange(event) {
        this.setState({ password: event.target.value });
    }

    /**
     * Sends HTTP post request to unlock key file - either successfully unlocks file or displays error message.
     * @param {string} password - the password the user inputs to unlock key file.
     */
    async checkPassword(password) {
        this.setState({loading: true, showWarning: false});
        const params = {
            "address": this.props.address,
            "password": password
        }
        const response = await createAndSendRequest("wallet_unlockKeyfile", params);

        //successful:
        if (response.data.result) {
            if (response.data.result[this.props.address] === "unlocked") {
                if (this.props.currentUnlocked) {
                    await this.props.lockKeyfile(this.props.currentUnlocked);
                }
                await this.props.updateData();
                this.setState({loading: false})
                this.props.toggle();
            }
        }

        //unsuccessful:
        else if (response.data.error) {
            this.setState({
                loading: false,
                showWarning: true,
                errorMsg: response.data.error.message,
                password: ""
            });
        }
    }

    render() {
        let render;
        if (this.state.loading) {
            //waiting for api response:
            render = <div style={{fontWeight: 400, fontSize: "14px", paddingTop: "15px"}}>
                <ImSpinner3 style={{animation: "rotation 2s infinite linear", marginRight: "5px"}}/>
                Unlocking key file...
            </div>
        }else {
            render = <div>
                <div className={"password-field"}>
                    <span> Password: </span>
                    <input type="password" name={"password"} className={"password-box"} placeholder={"Enter password"} value={this.state.password}
                           onChange={this.handleChange}/>
                </div>

                {this.state.showWarning
                    ? <div style={{margin: "10px", color: "red"}}> {this.state.errorMsg}</div>
                    : null
                }

                <div style={{paddingTop: "15px"}}>
                    <button className={"round-blue-button"} onClick={() => this.checkPassword(this.state.password)}>
                        Unlock
                    </button>
                </div>
            </div>
        }
        return (
            <div className={"unlock-popup"}>
                Unlock {this.props.keyfileName}
                <CloseButton handleClose={this.props.toggle} />
                {render}
            </div>
        );
    }
}

UnlockPopup.propTypes = unlockPopupPropTypes