import React from "react";
import PropTypes from "prop-types";
import {BiPlus, BsQuestionCircle} from "react-icons/all";
import "styles/balances-page/send/SendPopup.css";
import "styles/ToplButtons.css";
import {CloseButton} from "components/CloseButton";
import unselectedSwitch from "images/switch-unselected.svg";
import {ConfirmSendPopup} from "./online/ConfirmSendPopup";
import {SuccessfulBroadcastPopup} from "./online/SuccessfulBroadcastPopup";
import ReactTooltip from "react-tooltip";
import {SendRecipient} from "./SendRecipient";
import {UnsuccessfulTx} from "./UnsuccessfulTx";
import {Popup} from "components/Popup";
import {createAndSendRequest} from "modules/apiRequests";
import {capitalize, invalidRecipients, insufficientFunds} from "modules/sharedFunctions";
import {SendOfflineConnected} from "./offline/connected/SendOfflineConnected";
import {SendOfflineDisconnected} from "./offline/disconnected/SendOfflineDisconnected";

/**
 * Props for {@link SendTab}
 *  @property {string} boxType Type of asset to send: "arbit", "poly", or "asset"
 *  @property {number} currentBalance The balance of the currently opened keyfile.
 *  @property {string} fromAddress Public address for currently opened keyfile.
 *  @property {function} toggleSend For toggling send pop-up,
 *  @property {boolean} online If connected to bifrost = true
 *  @property {function} showRecentTxMsg To show message that a recent transaction has just occurred
 */
const sendTabPropTypes = {
    boxType: PropTypes.oneOf(["arbit", "poly", "asset"]).isRequired,
    currentBalance: PropTypes.number.isRequired,
    fromAddress: PropTypes.string.isRequired,
    toggleSend: PropTypes.func.isRequired,
    online: PropTypes.bool.isRequired,
    showRecentTxMsg: PropTypes.func.isRequired
}

/**
 * The component for the "Send" tab on the Balances page.
 */
export class SendTab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            sendOffline: !this.props.online,
            recipients: [
                {
                    address: "",
                    amount: null
                }
            ],
            indexCount: 0,
            confirmTxPopup: false,
            showInputError: false,
            showApiError: false,
            apiErrorMsg: "",
            txParams: [],
            successfulBroadcastPopup: false,
            changeAddress: "",
            consolidationAddress: "",
            txId: ""
        }
        this.handleChange = this.handleChange.bind(this);
        this.toggleSendOffline = this.toggleSendOffline.bind(this);
        this.addRecipient = this.addRecipient.bind(this);
        this.editRecipient = this.editRecipient.bind(this);
        this.removeRecipient = this.removeRecipient.bind(this);
        this.confirmTx = this.confirmTx.bind(this);
        this.sendTx = this.sendTx.bind(this);
        this.postBroadcast = this.postBroadcast.bind(this);
        this.toggleSuccessfulBroadcastPopup = this.toggleSuccessfulBroadcastPopup.bind(this);
    }

    /**
     * Function to handle changes to input elements: consolidationAddress and changeAddress
     * @param event - the event that prompted the onChange function call.
     */
    handleChange(event) {
        this.setState({[event.target.name]: event.target.value});
    }

    /**
     * Called when the user clicks on the "Send offline" switch.
     * Toggles between send offline and online.
     */
    toggleSendOffline() {
        this.setState({sendOffline: !this.state.sendOffline});
    }

    /**
     * Called when user clicks the "add recipient" button.
     * Adds an empty recipient to the list of recipients.
     */
    addRecipient() {
        this.setState({
            recipients: this.state.recipients.concat({
                address: "",
                amount: null
            })
        });
    }

    /**
     * Modifies a recipient in this.state with the given new info
     * @param {{address: string, amount: number}} recipientInfo - The new recipient info.
     * @param {number} index - The index of the recipient to be changed.
     */
    editRecipient(recipientInfo, index) {
        let newRecipients = this.state.recipients;
        newRecipients.splice(index, 1, recipientInfo);
        this.setState({recipients: newRecipients});
    }

    /**
     * Removes a recipient from this.state.recipients
     * @param {number} index - the index for the recipient to be removed.
     */
    removeRecipient(index) {
        let newRecipients = this.state.recipients;
        newRecipients.splice(index, 1);
        this.setState({recipients: newRecipients});
    }

    /**
     * Called when the user clicks the "Next" button.
     * If invalid inputs, shows error message. Else, shows {@link ConfirmSendPopup}.
     */
    confirmTx() {
        if (invalidRecipients(this.state.recipients, this.props.currentBalance)) {
            this.setState({showInputError: true});
        }else {
            if (!this.state.sendOffline) {
                this.setState({confirmTxPopup: !this.state.confirmTxPopup});
            }
        }
    }

    /** Sends HTTP post request for online transfer. */
    async sendTx() {
        const recipients = this.state.recipients.map(recp => [recp.address, recp.amount])
        let changeAddress = this.props.fromAddress
        if (this.state.changeAddress !== "") {
            changeAddress = this.state.changeAddress
        }
        let txParams = {
            "method": "topl_raw" + capitalize(this.props.boxType) + "Transfer",
            "params": [{
                "propositionType": "PublicKeyCurve25519",
                "sender": [this.props.fromAddress],
                "recipients": recipients,
                "changeAddress": changeAddress,
                "consolidationAddress": this.state.consolidationAddress,
                "fee": 1,
                "data": "",
                "online": true
            }]
        }
        this.setState({txParams: txParams});
        const response = await createAndSendRequest("onlineWallet_createTransaction", txParams);
        if (response.data.error) {
            this.setState({
                confirmTxPopup: false,
                showApiError: true,
                apiErrorMsg: response.data.error.message
            })
        } else {
            this.postBroadcast(response.data.result["txId"]);
        }
    }

    /**
     * Called after a successful online transaction.
     * Calls function to show "recent transaction" message and to display the {@link ./online/SuccessfulBroadcastPopup}.
     * @param txID
     */
    postBroadcast(txID) {
        this.props.showRecentTxMsg();
        this.setState({txId: txID}, ()=> this.toggleSuccessfulBroadcastPopup());
    }


    /** Open and closes the {@link SuccessfulBroadcastPopup} */
    toggleSuccessfulBroadcastPopup() {
        //If the SuccessfulBroadcastPopup is currently displayed, it should close the send tab before closing the popup.
        if (this.state.successfulBroadcastPopup) {
            this.props.toggleSend()
        }
        // toggles the SuccessfulBroadcastPopup and closes the ConfirmSendPopup
        this.setState({
            confirmTxPopup: false,
            successfulBroadcastPopup: !this.state.successfulBroadcastPopup
        })
    }

    /**
     * Renders the change adress input field
     * @returns {JSX.Element} the change address React element
     */
    renderChangeAddress() {
        let marginBelow = "0"
        if (this.props.boxType === "poly") {marginBelow = "20px"}
        return (<div className={"field"} style={{marginTop: "0px", marginBottom: marginBelow}}>
                                    <span> Change address:
                                        <BsQuestionCircle data-tip="" data-for={"change-address"} style={{verticalAlign: "middle", marginLeft: "5px"}}/>
                                    </span>
            <input type="text" name={"changeAddress"} className={"address-box"}
                   placeholder={this.props.fromAddress} value={this.state.changeAddress}
                   style={{height: "20px"}} onChange={this.handleChange}/>
            <ReactTooltip id={"change-address"} place={"bottom"} effect={"solid"}>
                <span style={{fontWeight: "500"}}> Address to send leftover Poly's to </span>
            </ReactTooltip>
        </div>)
    }

    /**
     * Renders the consolidation address input field
     * @returns {JSX.Element} the consolidation address React element
     */
    renderConsolidationAddress() {
        return (<div className={"field"} style={{marginTop: "0px", marginBottom: "20px"}}>
            <div style={{float: "left"}}>
                <div> Consolidation address: </div>
                <div>
                    (optional)
                    <BsQuestionCircle data-tip="" data-for={"consolidation-address"}
                                      style={{verticalAlign: "middle", marginLeft: "5px"}}/>
                </div>
            </div>
            <input type="text" name={"consolidationAddress"} className={"address-box"}
                   placeholder={"Enter address"} value={this.state.consolidationAddress}
                   style={{height: "20px"}} onChange={this.handleChange}/>
            <ReactTooltip id={"consolidation-address"} place={"bottom"} effect={"solid"}>
                <span style={{fontWeight: "500"}}> Address to send leftover {this.props.boxType}'s to </span>
            </ReactTooltip>
        </div>)
    }

    /**
     * Renders a pop-up based on the current state.
     * @returns {JSX.Element} the React pop-up component to be rendered.
     */
    renderPopup() {
        let currency;
        if (this.props.boxType === "poly") {
            currency = "POLY"
        }else if (this.props.boxType === "arbit") {
            currency = "ARB"
        }

        if (this.state.confirmTxPopup) {
            //confirm sending online tx
            return <ConfirmSendPopup toggle={this.confirmTx} fromAddress={this.props.fromAddress}
                                     recipients={this.state.recipients} boxType={currency}
                                     sendTx={this.sendTx}/>
        }
        else if (this.state.successfulBroadcastPopup) {
            //confirmation message after sending online tx
            return <SuccessfulBroadcastPopup toggle={this.toggleSuccessfulBroadcastPopup}
                                             txId={this.state.txId}/>
        } else if (this.state.showApiError) {
            //error pop-up
            return <Popup page={<UnsuccessfulTx signedTx={JSON.stringify(this.state.txParams)} errorMsg={this.state.apiErrorMsg}/>}
                          toggle={()=> this.setState({showApiError: false})}/>
        }
    }

    /**
     * Renders the "online" send tab.
     * @returns {JSX.Element} the React element for the send tab.
     */
    renderOnlineSendTab() {
        let errorMsg = "";
        if (this.state.showInputError) {
            if (this.state.recipients.filter(recpt => recpt.amount)) {
                if (insufficientFunds(this.state.recipients, this.props.currentBalance)) {
                    errorMsg = "You do not have enough balance for this transaction."
                }
            }
        }

        return (<div>
            {/* Display all of the recipient input fields */}
            <div style={{marginTop: "30px", marginBottom: "20px"}}>
                {this.state.recipients.map((recipient, index) => {
                    return <SendRecipient addressValue={recipient.address} amount={recipient.amount}
                                          boxType={this.props.boxType} removeFunc={() => this.removeRecipient(index)}
                                          showRemoveButton={this.state.recipients.length > 1}
                                          saveNewInfo={(newInfo) => this.editRecipient(newInfo, index)}
                                          error={this.state.showInputError}/>
                })}
            </div>

            <button className={"text-button"} style={{paddingLeft: "13px", paddingBottom: "20px"}} onClick={this.addRecipient}>
                <BiPlus className={"plus-symbol"}/>
                Add Recipient
            </button>

            {this.renderChangeAddress()}

            {/* Only render consolidation address for assets and arbits */}
            {this.props.boxType !== "poly" ? this.renderConsolidationAddress() : null}

            {/* Render send offline switch */}
            <span className={"field"} style={{paddingTop: "10px"}}>
                                Send offline
                                <img src={unselectedSwitch} className="offline-switch" alt="offline-switch" onClick={this.toggleSendOffline}/>
                            </span>

            <button onClick={this.confirmTx} className={"blue-square-button"}>
                Next
            </button>
            {this.renderPopup()}
            <span style={{color: "red"}}> {errorMsg} </span>
        </div>);
    }

    render() {
        /** close tab and show {@link ./online/SuccessfulBroadcastPopup} */
        if (this.state.successfulBroadcastPopup) {
            return this.renderPopup();
        }

        /** Display send tab depending on if the user wants to "Send offline" and
         * if the user is currently online (connected or disconnected from bifrost) */
        else{
            let sendTab;
            //if user chooses to send offline tx:
            if (this.state.sendOffline) {
                //user is online but wants to send an offline tx (create a raw tx)
                if (this.props.online) {
                    sendTab = <SendOfflineConnected toggleSendOffline={this.toggleSendOffline}
                                                    fromAddress={this.props.fromAddress} boxType={this.props.boxType}
                                                    recipients={this.state.recipients}
                                                    postBroadcast={this.postBroadcast} toggleSend={this.props.toggleSend}
                                                    changeAddress={this.state.changeAddress}
                                                    consolidationAddress={this.state.consolidationAddress}
                                                    currentBalance={this.props.currentBalance}/>
                }
                // user is offline:
                else {
                    sendTab = <SendOfflineDisconnected toggleSendOffline={this.toggleSendOffline}
                                                       fromAddress={this.props.fromAddress} boxType={this.props.boxType}
                                                       recipients={this.state.recipients}
                                                       changeAddress={this.state.changeAddress}
                                                       consolidationAddress={this.state.consolidationAddress}
                                                       currentBalance={this.props.currentBalance}/>
                }
            }
            //if user chooses to send online tx:
            else {
                sendTab = this.renderOnlineSendTab();
            }
            return (
                <div className={"send-tab"}>
                    <div> <CloseButton handleClose={this.props.toggleSend}/> </div>
                    {sendTab}
                </div>
            );
        }
    }
}

SendTab.propTypes = sendTabPropTypes