import React from 'react';
import PropTypes from "prop-types";
import {ChainProviderRow} from "./ChainProviderRow";

/**
 * Props for {@link ChainProviderTable}
 * @property {Array.<{name: string, chainProvider: string}>} chainProviders List of chain providers to display in table
 * @property {function} edit To edit a chain provider or its name
 * @property {function} remove To remove chain provider from list
 * @property {function} connected To update WalletApp state when connecting to new chainProvider
 * @property {string} currentChainProvider The chain provider that is currently connected.
 * @property {function} disconnectFn To disconnect from chain provider
 * @property {boolean} online True if gjallarhorn is connected to bifrost
 */
const chainProviderTablePropTypes = {
    chainProviders: PropTypes.arrayOf(PropTypes.shape({
        name: PropTypes.string,
        chainProvider: PropTypes.string
    })).isRequired,
    edit: PropTypes.func.isRequired,
    remove: PropTypes.func.isRequired,
    connected: PropTypes.func.isRequired,
    currentChainProvider: PropTypes.string.isRequired,
    disconnectFn: PropTypes.func.isRequired,
    online: PropTypes.bool.isRequired
}

/** The table to display and manage chain providers to connect to. Shown within the {@link SettingsPage}. */
export class ChainProviderTable extends React.Component {

    /**
     * Renders the headers for the table.
     * @returns {JSX.Element} - the row of headers
     */
    renderHeaders() {
        return (<tr>
            <th className={"table-header"} style={{fontSize: "14px"}}> Name </th>
            <th className={"table-header"} style={{fontSize: "14px"}}> Chain Provider </th>
            <th className={"table-header"} style={{fontSize: "14px"}}> Edit </th>
            <th className={"table-header"} style={{fontSize: "14px"}}> Remove </th>
            <th className={"table-header"}/>
        </tr>)
    }

    /**
     * Returns true if the given chain provider is currently connected. Returns false otherwise.
     * @param {string} chainProvider - the chain provider to check if connected.
     * @returns {boolean} - true if the chain provider is currently connected.
     */
    currentlyConnected(chainProvider) {
        return (chainProvider === this.props.currentChainProvider && this.props.online);
    }

    render() {
        return (
            <table style={{width: "90%", minWidth: "700px"}}>
                <thead>
                    {this.renderHeaders()}
                </thead>

                <tbody>
                    {this.props.chainProviders.map((info,index) => {
                        // if the name and chainProvider are empty strings, display in edit mode:
                        if (info.name === "" && info.chainProvider === "") {
                            return <ChainProviderRow name={info.name} chainProvider={info.chainProvider}
                                                     edit={(newInfo) => this.props.edit(index, newInfo)}
                                                     editMode={true} connected={this.props.connected}
                                                     currentlyConnected={this.currentlyConnected(info.chainProvider)}
                                                     disconnectFn={this.props.disconnectFn}
                                                     remove={()=>this.props.remove(index)}/>
                        } else {
                            return <ChainProviderRow name={info.name} chainProvider={info.chainProvider}
                                                     edit={(newInfo) => this.props.edit(index, newInfo)}
                                                     connected={this.props.connected}
                                                     currentlyConnected={this.currentlyConnected(info.chainProvider)}
                                                     disconnectFn={this.props.disconnectFn}
                                                     remove={()=>this.props.remove(index)}/>
                        }
                    })}
                </tbody>
            </table>
        )
    }
}

ChainProviderTable.propTypes = chainProviderTablePropTypes