import React from "react";
import axios from 'axios'
import {MyBalancesPage} from "./components/balances-page/MyBalancesPage"
import {TopBar} from "./components/full-page/TopBar";
import {LeftMenuBar} from "./components/full-page/LeftMenuBar";
import {KeysPage} from "./components/keys-page/KeysPage";
import './WalletApp.css';
import {SettingsPage} from "./components/settings-page/SettingsPage";
import {ErrorPopup} from "./components/ErrorPopup";
import {Popup} from "./components/Popup";
import {createAndSendRequest, createRequest} from "./modules/apiRequests";
import {communicationOptionsMap, languageOptionsMap, networkOptionsMap} from "./modules/dropdownOptions";
import {getFirstKeyFromValue} from "./modules/sharedFunctions";

/** The main application for the Gjallarhorn User-Interface */
export class WalletApp extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            expandedSideBar: true,
            page: "balances",
            currentKeyFile: null, //address of keyfile that is unlocked
            //map of keyfiles: address -> name, assets: array of {boxType, balance},
            // assetsList {code, balance}, unlocked: true or false
            keyFiles: new Map(),
            retrievingData: true,
            online: true,
            networkType: "", //name of the network connected to
            chainProvider: "bifrost-client@127.0.0.1:9087",
            connectionError: false,
            apiError: false,
            errorMsg: "",
            keyfileDir: "",
            communicationMode: "",
            apiKey: "",
            languageCode: "en" //English
        }
        this.toggleMenu = this.toggleMenu.bind(this);
        this.openBalances = this.openBalances.bind(this);
        this.openKeys = this.openKeys.bind(this);
        this.toggleConnection = this.toggleConnection.bind(this);
        this.changeNetwork = this.changeNetwork.bind(this);
        this.openSettings = this.openSettings.bind(this);
        this.setStateToConnected = this.setStateToConnected.bind(this);
        this.setStateToDisconnected = this.setStateToDisconnected.bind(this);
        this.updateData = this.updateData.bind(this);
        this.closeError = this.closeError.bind(this);
        this.showErrorPopup = this.showErrorPopup.bind(this);
        this.switchLanguage = this.switchLanguage.bind(this);
    }

    /** Retrieves all of the data from the back-end when the component mounts. */
    async componentDidMount() {
        await this.updateData()
    }

    /** Updates the data by requesting the current state from the back-end. */
    async updateData() {
        this.setState({retrievingData: true})

        const stateRequest = createRequest("wallet_getCurrentState", {});
        const getConnectionStatus = createRequest("onlineWallet_getConnection", {});
        const headers = {"x-api-key": process.env.REACT_APP_API_KEY}
        axios.all([
            axios.post(process.env.REACT_APP_API_URL, stateRequest, {headers}),
            axios.post(process.env.REACT_APP_API_URL, getConnectionStatus, {headers})
        ]).then(async(res) => {
            //error handling:
            if (res[0].data.error) {
                this.setState({
                    apiError: true,
                    errorMsg: "From wallet_getCurrentState request: " + res[0].data.error.message
                })
            }
            else if (res[1].data.error) {
                this.setState({
                    apiError: true,
                    errorMsg: "From onlineWallet_getConnection request: " + res[1].data.error.message
                })
            }

            else {
                //current state should contains: keys, networkPrefix, keyfileDirectory, apiKey, and communicationMode
                const currentState = res[0].data.result;
                this.initializeKeyfiles(currentState["keys"]);
                this.initializeNetwork(currentState["networkPrefix"])
                this.setState({
                    keyfileDir: currentState["keyfileDirectory"],
                    online: res[1].data.result["connectedToBifrost"],
                    communicationMode: getFirstKeyFromValue(communicationOptionsMap, currentState["communicationMode"]),
                    apiKey: currentState["apiKey"]
                });

                //Request balances if there are existing key files.
                if (Object.keys(currentState["keys"]).length > 0) {
                    const responseTwo = await createAndSendRequest("wallet_balances", {});
                    this.parseBalances(responseTwo.data.result);
                }
            }

            this.setState({retrievingData: false});
        }).catch (error => {
            console.log(error)
            this.setState({
                retrievingData: false,
                online: false,
                connectionError: true,
                errorMsg: error
            })
        })
    }

    /**
     * Initializes key files
     * @param {Map<string, string>} addressesMap: a map of addresses to "locked" or "unlocked"
     */
    initializeKeyfiles(addressesMap) {
        let keyFiles = new Map();
        const addresses = Object.keys(addressesMap)
        let currentKeyfile = null
        addresses.forEach( (addr, index) => {
            let unlocked = false
            if (addressesMap[addr] === "unlocked") {
                unlocked = true
                currentKeyfile = addr
            }
            const simpleAssets = [
                {boxType: "poly", balance: 0},
                {boxType: "arbit", balance: 0}
            ]
            keyFiles.set(addr,
                {
                    name: "KeyFile " + (index+1).toString(),
                    assets: simpleAssets,
                    assetsList: [],
                    unlocked: unlocked
                });
            index++;
        })

        this.setState({
            currentKeyFile: currentKeyfile,
            keyFiles: keyFiles
        })
    }

    /**
     * Initializes the network
     * @param {number} networkPrefix - the byte prefix that corresponds to a network
     */
    initializeNetwork(networkPrefix) {
        let networkName = networkOptionsMap.get(networkPrefix);
        //if it undefined, set to "Local"
        if (!networkName) {
            networkName = "Local"
        }
        this.setState({networkType: networkName});
    }

    /**
     * Parses the response from a "balance" request and saves the information to the state's keyfiles.
     * @param {Object} response - the api response from a "balances" request
     */
    parseBalances(response) {
        const pubKeys = Object.keys(response);
        let keyFiles = this.state.keyFiles
        pubKeys.forEach(addr => {
            const balanceObject = response[addr]
            //set poly and arbit balances:
            const simpleAssets = [
                {boxType: "poly", balance: balanceObject["PolyBox"]},
                {boxType: "arbit", balance: balanceObject["ArbitBox"]}
            ]

            // set asset balances:
            const assetsList = []
            const assetCodes = Object.keys(balanceObject).filter(key => !(key === "PolyBox" || key === "ArbitBox"))
            assetCodes.forEach(assetCode => {
                assetsList.push(
                    {
                        code: assetCode,
                        balance: balanceObject[assetCode]
                    })
            })
            keyFiles.set(addr,
                {
                    name: keyFiles.get(addr).name,
                    assets: simpleAssets,
                    assetsList: assetsList,
                    unlocked: keyFiles.get(addr).unlocked
                });
        })
        this.setState({keyFiles: keyFiles})
    }

    /** Sends HTTP post request to connect or disconnect from Bifrost. */
    async toggleConnection() {
        let changeConnection;
        if (this.state.online) {
            changeConnection = createRequest("onlineWallet_disconnectFromBifrost", {});
        }else{
            changeConnection = createRequest("onlineWallet_connectToBifrost",
                {"chainProvider": this.state.chainProvider});
        }
        const headers = {"x-api-key": process.env.REACT_APP_API_KEY}
        const response = await axios.post(process.env.REACT_APP_API_URL, changeConnection, {headers});
        if (response.data.error) {
            this.showErrorPopup(response.data.error.message);
        }else {
            await this.updateData();
        }
    }

    /**
     * Sets the state to online = true with the new chain provider (from the settings page)
     * @param {string} chainProvider - the new chain provider connected to.
     */
    setStateToConnected(chainProvider) {
        this.setState({
            online: true,
            chainProvider: chainProvider
        });
    }

    /** Sets this.state.online to false */
    setStateToDisconnected() {
        this.setState({online: false})
    }

    /** Expands or collapses left side-bar menu */
    toggleMenu() {
        this.setState({expandedSideBar: !this.state.expandedSideBar});
    }

    /** Opens the "balances" page */
    openBalances() {
        this.setState({page: "balances"});
    }

    /** Opens the keys page */
    openKeys() {
        this.setState({page: "keys"});
    }

    /** Opens the settings page */
    openSettings() {
        this.setState({page: "settings"});
    }

    /**
     * Displays error pop-up {@link ErrorPopup} with given message.
     * @param {string} msg - the error message received from the API.
     */
    showErrorPopup(msg) {
        this.setState({
            apiError: true,
            errorMsg: msg
        })
    }

    /** Closes {@link ErrorPopup} */
    closeError() {
        this.setState({apiError: false});
    }

    /**
     * Changes the network to the given new network.
     * @param {string} newNetwork - the name of the network to switch to.
     */
    async changeNetwork(newNetwork) {
        this.setState({networkType: newNetwork});
        await this.updateData();
    }

    /**
     * Switches the current language for the word list.
     * @param {string} newLanguage - the new language to switch to.
     */
    switchLanguage(newLanguage) {
        const newLangCode = languageOptionsMap.get(newLanguage);
        this.setState({languageCode: newLangCode});
    }

    render() {
        let polysBalance, arbitsBalance, page;
        let connectionError = "Can't connect to Gjallarhorn, make sure the application is running. " +
            "Please restart the app and refresh the page. The error message is " + this.state.errorMsg;
        let assets = [];
        let assetsList = [];

        //If there are keyfiles and the currentkeyfiles contains assets, set poly and arbit balances and assets values.
        if (this.state.keyFiles.size > 0 && this.state.keyFiles.get(this.state.currentKeyFile)) {
            polysBalance = this.state.keyFiles.get(this.state.currentKeyFile).assets
                .filter(asset => asset.boxType === "poly")[0].balance;

            arbitsBalance = this.state.keyFiles.get(this.state.currentKeyFile).assets
                .filter(asset => asset.boxType === "arbit")[0].balance;

            assets = this.state.keyFiles.get(this.state.currentKeyFile).assets
            if (this.state.keyFiles.get(this.state.currentKeyFile).assetsList) {
                assetsList = this.state.keyFiles.get(this.state.currentKeyFile).assetsList
            }
        }

        //Sets the current page depending on the state
        if (this.state.page === "balances") {
            page = <MyBalancesPage publicAddress={this.state.currentKeyFile} assets={assets} assetsList={assetsList}
                                   openKeys={this.openKeys} online={this.state.online} retrievingData={this.state.retrievingData}
                                   allLocked={!this.state.currentKeyFile && this.state.keyFiles.size > 0}
                                   updateData={this.updateData}/>
        } else if (this.state.page === "keys") {
            page = <KeysPage keyFiles={this.state.keyFiles} currentKeyFile={this.state.currentKeyFile}
                             retrievingData={this.state.retrievingData} updateData={this.updateData}
                             currentLanguage={this.state.languageCode}/>
        } else if (this.state.page === "settings") {
            page = <SettingsPage  chainProvider={this.state.chainProvider} network={this.state.networkType}
                                  online={this.state.online} connected={this.setStateToConnected}
                                  disconnectFn={this.setStateToDisconnected} keyFileDir={this.state.keyfileDir}
                                  updateData={this.updateData} currentLanguageCode={this.state.languageCode}
                                  switchLanguage={this.switchLanguage} apiKey={this.state.apiKey}
                                  communicationMode={this.state.communicationMode}/>
        }

        //sets styles for the side bar, depending on if it is expanded or collapsed:
        let leftMargin, openPageWidth;
        if (this.state.expandedSideBar) {
            leftMargin = "256px";
            openPageWidth = "80vw";
        }else{
            leftMargin = "60px";
            openPageWidth = "94vw";
        }

        if (this.state.networkType === "") {
            return (
                <div className={"page"} style={{marginLeft: leftMargin}}>
                    <div className={"open-page"} style={{width: openPageWidth}}>
                        {page}
                    </div>
                    {<LeftMenuBar currentPage={this.state.page} expandedSideBar={this.state.expandedSideBar}
                                  openBalances={this.openBalances} openKeys={this.openKeys} polysBalance={polysBalance}
                                  arbitsBalance={arbitsBalance} openSettings={this.openSettings}/>}
                    {this.state.connectionError ? <Popup page={<ErrorPopup message={connectionError}/>}
                                                         toggle={()=> null}/> : null}
                    {this.state.apiError
                        ? <Popup page={<ErrorPopup message={this.state.errorMsg}/>} toggle={this.closeError}/>
                        : null
                    }
                </div>
            );
        }else{
            return (
                <div className={"page"} style={{marginLeft: leftMargin}}>
                    <div className={"open-page"} style={{width: openPageWidth}}>
                        <TopBar toggleMenu={this.toggleMenu} networkType={this.state.networkType}
                                online={this.state.online} toggleConnection={this.toggleConnection}
                                changeNetwork={this.changeNetwork} showError={this.showErrorPopup}/>
                        {page}
                    </div>
                    {<LeftMenuBar currentPage={this.state.page} expandedSideBar={this.state.expandedSideBar}
                                  openBalances={this.openBalances} openKeys={this.openKeys} polysBalance={polysBalance}
                                  arbitsBalance={arbitsBalance} openSettings={this.openSettings}/>}
                    {this.state.apiError
                        ? <Popup page={<ErrorPopup message={this.state.errorMsg}/>} toggle={this.closeError}/>
                        : null
                    }
                </div>
            );
        }

    }
}

