import React from "react"
import PropTypes from "prop-types";
import "styles/full-page/BalancesSideBar.css"

/**
 * Props for {@link BalancesSideBar}
 * @property {number} polysBalance Poly balance for currently unlocked key file
 * @property {number} arbitsBalance Arbit balance for currently unlocked key file
 */
const balancesSideBarPropTypes = {
    polysBalance: PropTypes.number.isRequired,
    arbitsBalance: PropTypes.number.isRequired
}

/**
 * The balances list displayed under "My Balances" in the {@link ./LeftMenuBar}.
 * Displays current poly and arbit balances.
 */
export class BalancesSideBar extends React.Component {

    render() {
        let balanceList = [
            (<ul className={"balances-menu-item"}> Polys
                <span className={"balance-amount"} style={{paddingLeft: "83px"}} key={"poly-balance"}> {this.props.polysBalance} </span>
            </ul>),
            (<ul className={"balances-menu-item"}> Arbits
                <span className={"balance-amount"} style={{paddingLeft: "80px"}} key={"arbit-balance"}> {this.props.arbitsBalance} </span>
            </ul>)
        ];

        return (
            <div>
                {balanceList}
            </div>
        )
    }
}

BalancesSideBar.propTypes = balancesSideBarPropTypes