import React from "react";
import PropTypes from "prop-types";
import {ImSpinner3} from "react-icons/all";
import "styles/ToplButtons.css";
import {CloseButton} from "../CloseButton";
import {createAndSendRequest} from "modules/apiRequests";

/**
 * Props for {@link ImportKeyfilePopup}
 *     @property {function} toggle To open/close the popup
 *     @property {string} language Language code used for seed phrase language when importing key file
 *     @property {function} lockCurrent To lock current keyfile when importing new keyfile and update data accordingly.
 */
const importKeyfilePopupPropTypes = {
    toggle: PropTypes.func.isRequired,
    language: PropTypes.string.isRequired,
    lockCurrent: PropTypes.func.isRequired
}

/** Import keyfile pop-up. Displayed when user clicked "Import Keyfile" button on {@link KeysPage} */
export class ImportKeyfilePopup extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            mnemonicPhrase: "",
            password: "",
            errorMsg: null,
            loading: false
        }
        this.handleChange = this.handleChange.bind(this);
        this.addKeyfile = this.addKeyfile.bind(this);
    }

    /**
     * Function to handle changes to input elements: mnemonic phrase and password.
     * @param event - the event that triggers the onChange function.
     */
    handleChange(event) {
        this.setState({[event.target.name]: event.target.value});
    }

    /** Sends HTTP post request to importKeyfile with password and mnemonic phrase. */
    async addKeyfile() {
        this.setState({loading: true});
        const params = {
            "password": this.state.password,
            "seedPhrase": this.state.mnemonicPhrase,
            "seedPhraseLang": this.props.language
        }
        const response = await createAndSendRequest("wallet_importKeyfile", params);
        if (response.data.error) {
            this.setState({errorMsg: response.data.error.message, loading: false});
        }else{
            //locks currently unlocked file (since the newly import keyfile is now unlocked)
            //and updates the data with new keyfile.
            await this.props.lockCurrent();
            this.setState({loading: false});
            this.props.toggle();
        }
    }


    render() {
        return (
            <div className={"new-keyfile-popup"} style={{height: "480px"}}>
                <CloseButton handleClose={this.props.toggle}/>
                <h1 style={{marginTop: "5px"}}> Import keyfile </h1>
                {this.state.loading
                    ? <div style={{fontWeight: 400, fontSize: "14px", paddingTop: "15px"}}>
                        <ImSpinner3 style={{animation: "rotation 2s infinite linear", marginRight: "5px"}}/>
                        Importing key file...
                    </div>
                    :<div>
                        {/* Mnemonic phrase input field: */}
                        <div className={"name-field"} style={{paddingBottom: "20px"}}>
                            <span> Mnemonic phrase: </span>
                            <textarea className={"input-style"} name={"mnemonicPhrase"} onChange={this.handleChange}
                                      style={{width: "90%", height: "90px", fontFamily: "Roboto", padding: "5px"}}
                                      placeholder={"Enter mnemonic phrase"} value={this.state.mnemonicPhrase}/>
                        </div>

                        {/* Password input field: */}
                        <div className={"password"} style={{float: "none"}}>
                            <span> Enter password: </span>
                            <input className={"input-style"} style={{width: "100%"}} type={"password"} name={"password"}
                                   placeholder={"Password"} value={this.state.password} onChange={this.handleChange}/>
                        </div>

                        {this.state.errorMsg ? <div style={{color: "red"}}> {this.state.errorMsg}</div> : null}

                        {/* import button: */}
                        <button className={"blue-square-button"} style={{float: "right"}}
                                onClick={async () => { await this.addKeyfile()}}>
                            Import
                        </button>
                    </div>
                }
            </div>
        );
    }
}

ImportKeyfilePopup.propTypes = importKeyfilePopupPropTypes