import axios from "axios";

/** This module contains helper functions for sending api requests. */

/** Create and sends a single HTTP post request through Axios
 * @param method {String} the method name for the rpc request
 * @param params {Object} json object of parameters for the given method
 * @returns {Promise<AxiosResponse<any>>} the api response
 */
async function createAndSendRequest(method, params) {
    const headers = {"x-api-key": process.env.REACT_APP_API_KEY}
    return await axios.post(process.env.REACT_APP_API_URL, createRequest(method, params), {headers});
}

/**
 * Creates a json-rpc request
 * @param method {String} the method name for the rpc request
 * @param params {Object} the parameters for the given method
 * @returns {{method: String, id: string, jsonrpc: string, params: Object[]}} correctly formed data for json-rpc request
 */
function createRequest (method, params) {
    return {
        jsonrpc: "2.0",
        id: "2",
        method: method,
        params: [params]
    }
}

export { createRequest, createAndSendRequest }