import {LinkNext} from "grommet-icons";
import React from "react";
import PropTypes from "prop-types";
import {TxDataTable} from "../TxDataTable";
import {createAndSendRequest} from "modules/apiRequests";

/**
 * Props for {@link ConfirmSendTx}:
 *  @property {Object} signedTx The signed transaction to broadcast (json object)
 *  @property {function} postBroadcastAttempt Called when broadcast response is received.
 */
const confirmSendTxPropTypes = {
    signedTx: PropTypes.object.isRequired,
    postBroadcastAttempt: PropTypes.func.isRequired,
}

/**
 * Component displayed to confirm broadcasting an offline tx.
 * This step is called "sendTx" in {@link ../SendOfflineConnected}.
 */
export class ConfirmSendTx extends React.Component {
    constructor(props) {
        super(props);
        this.broadcastTx = this.broadcastTx.bind(this);
    }

    /** Sends HTTP post request to broadcast a transaction */
    async broadcastTx() {
        const params = {
            "method": "topl_broadcastTx",
            "params": [{
                "tx": this.props.signedTx
            }]
        }
        const response = await createAndSendRequest("onlineWallet_broadcastTx", params);
        this.props.postBroadcastAttempt(response);
    }

    render() {

        return (
            <div style={{paddingTop: "15px", paddingLeft: "15px", fontWeight: "bold"}}>
                Confirm Transaction:
                <TxDataTable rawTx={this.props.signedTx} />
                <button className={"blue-square-button"} onClick={this.broadcastTx}>
                    Send
                    <LinkNext color={"white"} style={{paddingLeft: "10px", verticalAlign: "middle"}}/>
                </button>
            </div>
        )
    }
}

ConfirmSendTx.propTypes = confirmSendTxPropTypes