import React from "react";
import PropTypes from "prop-types";
import {Menu} from "grommet-icons/es6";
import {MdWifi, RiWifiOffFill} from "react-icons/all";
import "styles/full-page/TopBar.css"
import {NetworkDropdown} from "./NetworkDropdown";
import unselectedSwitch from "images/switch-unselected.svg";
import selectedSwitch from "images/switch-selected.svg";


/**
 * Props for {@link TopBar}
 *  @property {function} toggleMenu Function to toggle side menu
 *  @property {string} networkType The currently selected network
 *  @property {function} toggleConnection Used to connect or disconnect to bifrost
 *  @property {boolean} online If currently connected to bifrost
 *  @property {function} changeNetwork To switch networks
 *  @property {function} showError To show error message
 */
const topBarPropTypes = {
    toggleMenu: PropTypes.func.isRequired,
    networkType: PropTypes.string.isRequired,
    toggleConnection: PropTypes.func.isRequired,
    online: PropTypes.bool.isRequired,
    changeNetwork: PropTypes.func.isRequired,
    showError: PropTypes.func.isRequired
}

/**
 * The top bar that appears at the top of all pages.
 * Displays: hamburger menu (to toggle {@link ./LeftMenuBar}), connectivity switch, and {@link NetworkDropdown}
 */
export class TopBar extends React.Component {

    render() {
        return(
            <div className={"top-bar"}>

                {/* Hamburger menu button to expand/collapse side menu: */}
                <button className={"side-menu-button"} onClick={this.props.toggleMenu}>
                    <Menu style={{height: "20px", paddingLeft: "24px", paddingTop: "15px"}}/>
                </button>

                {/* Top right icons: connectivity switch and network dropdown: */}
                <div className={"top-right-icons"}>
                    {this.props.online
                        ? <span>
                            <MdWifi style={{height: "30px", width: "30px", verticalAlign: "middle"}} />
                            <span style={{verticalAlign: "middle"}}>
                                Connected
                            </span>
                            <img src={selectedSwitch} className="offline-switch" onClick={this.props.toggleConnection}
                                 style={{paddingRight: "20px", width: "35px", marginLeft: "5px"}} alt={"online-switch"}/>
                        </span>
                        : <span>
                            <RiWifiOffFill style={{height: "27px", width: "27px", verticalAlign: "middle"}}/>
                            <span style={{verticalAlign: "middle"}}>
                                Disconnected
                            </span>
                            <img src={unselectedSwitch} className="offline-switch" onClick={this.props.toggleConnection}
                                 style={{paddingRight: "20px", width: "35px", marginLeft: "5px"}} alt={"offline-switch"}/>
                        </span>
                    }

                    <NetworkDropdown currentNetwork={this.props.networkType} online={this.props.online}
                                     changeNetwork={this.props.changeNetwork} showError={this.props.showError}/>
                </div>
            </div>
        );
    }
}

TopBar.propTypes = topBarPropTypes