import React from "react";
import PropTypes from "prop-types";

/**
 * Props for {@link ConfirmRemovePopup}
 *  @property {function} removeFn The function used to remove the chain provider
 *  @property {function} toggle For closing the pop-up
 */
const confirmRemovePopupPropTypes = {
    removeFn: PropTypes.func.isRequired,
    toggle: PropTypes.func.isRequired
}

/** Popup to confirm removing a chain provider - called from {@link ChainProviderRow}*/
export class ConfirmRemovePopup extends React.Component {
    render() {
        return(
            <div style={{marginTop: "40px"}}>
                Are you sure you want to remove this chain provider?
                <div style={{marginTop: "40px", float: "right"}}>
                    <button className={"blue-square-button"} style={{float: "none"}} onClick={this.props.removeFn}> Yes </button>
                    <button className={"blue-square-button"} style={{float: "none"}} onClick={this.props.toggle}> No </button>
                </div>
            </div>
        )
    }
}

ConfirmRemovePopup.propTypes = confirmRemovePopupPropTypes