import React from "react";
import arbit from "images/arbitcoin.png";
import poly from "images/poly.jpeg";
import "styles/balances-page/Balance.css"
import {Send} from "grommet-icons";
import {SendTab} from "./send/SendTab";
import PropTypes from 'prop-types';


/** Props for {@link Balance}
 * @property {string} boxType "arbit", "poly", or "asset"
 * @property {number} balance Balance for this asset
 * @property {string} publicAddress The address of the currently unlocked keyfile
 * @property {boolean} online True if currently connected to bifrost
 * @property {function} showRecentTxMsg To show a message when a transaction was recently sent
 * */
const balancePropTypes = {
    boxType: PropTypes.oneOf(["arbit", "poly", "asset"]).isRequired,
    balance: PropTypes.number.isRequired,
    publicAddress: PropTypes.string.isRequired,
    online: PropTypes.bool.isRequired,
    showRecentTxMsg: PropTypes.func.isRequired
}

/**
 * A balance component for "Poly" or "Arbit" assets, which includes the "Send" button.
 */
export class Balance extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            sendOpen: false
        }
        this.toggleSend = this.toggleSend.bind(this);
    }

    /** Open and closes the send tab. */
    toggleSend() {
        this.setState({
            sendOpen: !this.state.sendOpen
        });
    }

    render() {
        let icon, amount, sendPopup;
        // initialize currency icon and balance with currency code:
        if (this.props.boxType === "arbit") {
            icon = <img className={"icon"} src={arbit}  alt={"ARbit icon"}/>
            amount = <span className={"amount"}> {this.props.balance.toLocaleString()} ARB </span>
        }else if (this.props.boxType === "poly") {
            icon = <img className={"icon"} src={poly}  alt={"Poly icon"}/>
            amount = <span className={"amount"}> {this.props.balance.toLocaleString()} POLY </span>
        }

        // set the style of the "SendTab" button depending on open/closed.
        let sendStyle = "action-button-closed"
        if (this.state.sendOpen) {
            sendStyle = "action-button-opened"
            sendPopup = <SendTab boxType={this.props.boxType} fromAddress={this.props.publicAddress}
                                 toggleSend={this.toggleSend} online={this.props.online}
                                 currentBalance={this.props.balance} showRecentTxMsg={this.props.showRecentTxMsg}/>
        }

        return (
            <div className={"balance-row"}>
                {icon}
                {amount}
                <div className={"action-buttons"}>
                    <button className={sendStyle} onClick={this.toggleSend}>
                        <span> Send </span>
                        <Send style={{verticalAlign: "middle"}}/>
                    </button>
                </div>
                {sendPopup}
            </div>
        )
    }
}

Balance.propTypes = balancePropTypes