import React from "react";
import PropTypes from "prop-types";
import "styles/Popup.css";
import {CloseButton} from "./CloseButton";

/**
 * Props for {@link Popup}
 *  @property {JSX.Element} page the dom.element to be rendered within the pop-up
 *  @property {function} toggle To close the pop-up
 *  @property {string} width (optional) width of pop-up
 *  @property {string} height (optional) height of pop-up
 */
const popupPropTypes = {
    page: PropTypes.element.isRequired,
    toggle: PropTypes.func.isRequired,
    width: PropTypes.string,
    height: PropTypes.string
}

/** Component used to display other components within a pop-up. */
export class Popup extends React.Component {

    render() {
        return (
            <div className={"popup"} style={{width: this.props.width, height: this.props.height}}>
                <CloseButton style={{marginRight: "20px"}} handleClose={this.props.toggle} />
                {this.props.page}
            </div>
        )
    }
}

Popup.propTypes = popupPropTypes