import React from 'react';
import PropTypes from "prop-types";
import "styles/balances-page/send/TxDataTable.css";
import {NewBox} from "./NewBox";

/**
 * Props for {@link TxDataTable}
 * @property {Object} rawTx The raw tx object to be displayed in the table
 */
const txDataTablePropTypes = {
    rawTx: PropTypes.object.isRequired
}

/** Component that displays a table of a transaction's data (inputs and outputs) */
export class TxDataTable extends React.Component {

    /**
     * Renders the body of the table (the keys and values of the transaction json object)
     * @returns {JSX.Element[]} - the rows of the transaction data table
     */
    renderBody() {
        const data = this.props.rawTx
        const keys = Object.keys(data)
        return keys.map((key) => {
            let output = <td className={"output-row"}>{JSON.stringify(data[key])}</td>
            if (key === "newBoxes") {
                output = <td className={"output-row"}> {data[key].map(box => {
                    return ( <NewBox data={box}/>)
                })} </td>;
            }
            return <tr>
                <td className={"data-row"}>{key}</td>
                {output}
            </tr>
        });
    }

    render() {
        return(
            <div>
                <table className={"data-table"}>
                    <thead>
                        <tr>
                            <th className={"data-header"}>Inputs: </th>
                            <th className={"data-header"} style={{textAlign: "left", display: "inline-block", width: "65%"}}>Outputs: </th>
                        </tr>
                    </thead>
                    <tbody>
                        {this.renderBody()}
                    </tbody>
                </table>
            </div>
        )
    }
}

TxDataTable.propTypes = txDataTablePropTypes