import React from 'react';
import PropTypes from "prop-types";
import {Down, Key, Money, SettingsOption, Up} from "grommet-icons";
import {BalancesSideBar} from "./BalancesSideBar";
import topl from "images/topl-logo.png";
import toplLeaves from "images/topl-leaves.png"
import "styles/full-page/LeftMenuBar.css"
import ReactTooltip from "react-tooltip";
import {BiWallet} from "react-icons/all";

/**
 * Props for {@link LeftMenuBar}
 * @property {string} currentPage Indicates which page is open: balances, keys, or settings.
 * @property {boolean} expandedSideBar Indicates whether the side bar is expanded or collapsed.
 * @property {function} openBalances Opens {@link MyBalancesPage}
 * @property {function} openKeys Opens {@link KeysPage}
 * @property {function} openSettings Opens {@link SettingsPage}
 * @property {number} polysBalance The poly balance for the currently unlocked key.
 * @property {number} arbitsBalance The arbit balance for the currently unlocked key.
 */
const leftMenuBarPropTypes = {
    currentPage: PropTypes.string.isRequired,
    expandedSideBar: PropTypes.bool.isRequired,
    openBalances: PropTypes.func.isRequired,
    openKeys: PropTypes.func.isRequired,
    openSettings: PropTypes.func.isRequired,
    polysBalance: PropTypes.number.isRequired,
    arbitsBalance: PropTypes.number.isRequired
}

/** Collapsible left menu side-bar which displays the different pages: My Balances, My Keys, Settings */
export class LeftMenuBar extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            walletDropdown: true
        }
        this.toggleWalletDropdown = this.toggleWalletDropdown.bind(this);
    }

    /** Open and closes the "Wallet" dropdown menu */
    toggleWalletDropdown() {
        this.setState({walletDropdown: !this.state.walletDropdown});
    }

    /** Renders (horizontally) expanded side bar */
    renderExpandedSideBar() {
        let leftMenuItems;

        //initializes the style classes for the options within the menu:
        let balancesStyle = "sub-menu-item";
        let keysStyle = "sub-menu-item";
        let walletStyle = "menu-button-clicked";
        let settingsStyle = "menu-button";

        let walletClick = this.toggleWalletDropdown;

        // changes styles based on which page is currently opened:
        if (this.props.currentPage === "balances") {
            balancesStyle = "sub-menu-item-open";
        }

        if (this.props.currentPage === "keys") {
            keysStyle = "sub-menu-item-open";
        }

        if (this.props.currentPage === "settings") {
            settingsStyle = "menu-button-clicked";
            walletStyle = "menu-button";
            //if settings page is opened, then clicked on "Wallet" will open the "My Balances" page:
            walletClick = this.props.openBalances;
        }

        leftMenuItems = (
            <div>
                {/* Wallet menu button: */}
                <button onClick={walletClick} className={walletStyle} style={{width: "256px"}}>
                    <BiWallet className={"menu-icon"}/>
                    <span className={"menu-item"}>Wallet</span>
                    {this.state.walletDropdown
                        ? <Up color="black" style={{float: "right", paddingRight: "20px"}}/>
                        : <Down color="black" style={{float: "right", paddingRight: "20px"}}/>}
                </button>

                {/* Wallet dropdown: "My Balances" and "My Keys" */}
                {this.state.walletDropdown
                    ? <div>
                        <button className={balancesStyle} onClick={this.props.openBalances}>
                            My Balances
                        </button>

                        <ul className={"balances-list"}>
                            <BalancesSideBar  arbitsBalance={this.props.arbitsBalance}
                                              polysBalance={this.props.polysBalance}/>
                        </ul>

                        <button className={keysStyle} onClick={this.props.openKeys} style={{marginBottom: "10px"}}>
                            My Keys
                        </button>
                    </div>
                    : null}

                {/* Settings menu button: */}
                <button onClick={this.props.openSettings} className={settingsStyle} style={{width: "256px"}}>
                    <SettingsOption className={"menu-icon"} color={"black"}/>
                    <span className={"menu-item"}>Settings</span>
                </button>
            </div>
        )
        return(
            <div className={"left-menu"}>
                <img src={topl} className="topl-logo" alt="App-logo"/>
                <div className="left-menu-items">
                    {leftMenuItems}
                </div>
            </div>
        )

    }

    /**
     * Renders (horizontally) collapsed side bar:
     * @returns {JSX.Element} the collapsed side bar React element.
     */
    renderCollapsedSideBar() {

        //initializes the styling for the menu options:
        let balancesColor = "#444444";
        let keyColor = "#444444";
        let walletStyle = "menu-button-clicked";
        let settingsStyle = "menu-button";

        // changes styles based on which page is currently opened:
        if (this.props.currentPage === "balances") {
            balancesColor="#26A69A";
        }
        if (this.props.currentPage === "keys") {
            keyColor="#26A69A";
        }

        if (this.props.currentPage === "settings") {
            settingsStyle = "menu-button-clicked";
            walletStyle = "menu-button";
        }

        return(
            <div className={"collapsed-left-menu"}>
                <img src={toplLeaves} className="topl-small-logo" alt="App-logo"/>
                <div className={"collapsed-menu-items"}>

                    {/* Wallet menu button: */}
                    <button className={walletStyle} style={{width: "60px"}}>
                        <BiWallet className={"menu-icon"} style={{paddingLeft: "10px"}}/>
                    </button>

                    {/* "My Balances" menu button: */}
                    <button data-tip="" data-for={"balancesTip"} onClick={this.props.openBalances} className={"sub-buttons"}>
                        <Money color={balancesColor}/>
                    </button>
                    <ReactTooltip id={"balancesTip"} place={"right"} effect={"solid"} arrowColor={"transparent"}>
                        My Balances
                    </ReactTooltip>

                    {/* "My Keys" menu button: */}
                    <button data-tip="" data-for={"keysTip"} onClick={this.props.openKeys} className={"sub-buttons"}
                            style={{marginBottom: "25px"}}>
                        <Key color={keyColor} />
                    </button>
                    <ReactTooltip id={"keysTip"} place={"right"} effect={"solid"} arrowColor={"transparent"} data-offset={{'top': 10, 'left': 10}}>
                        My Keys
                    </ReactTooltip>

                    {/* Settings menu button: */}
                    <button data-tip="" data-for={"settingsTip"} onClick={this.props.openSettings}
                            className={settingsStyle} style={{width: "60px"}}>
                        <SettingsOption className={"menu-icon"} color={"black"} style={{paddingLeft: "10px"}}/>
                    </button>
                    <ReactTooltip id={"settingsTip"} place={"right"} effect={"solid"} arrowColor={"transparent"} data-offset={{'top': 10, 'left': 10}}>
                        Settings
                    </ReactTooltip>
                </div>
            </div>
        )
    }


    render() {
        if (this.props.expandedSideBar) {
            return <div> {this.renderExpandedSideBar()} </div>
        }else{
            return <div> {this.renderCollapsedSideBar()} </div>
        }
    }
}

LeftMenuBar.propTypes = leftMenuBarPropTypes