import React from "react";
import PropTypes from "prop-types";
import {ImSpinner3} from "react-icons/all";
import {CloseButton} from "../CloseButton";
import unselectedBox from "images/unselected.svg";
import selectedBox from "images/selected.svg";
import "styles/keys-page/MnemonicPhrasePopup.css";
import {createAndSendRequest} from "modules/apiRequests";

/**
 * Props for {@link MnemonicPhrasePopup}:
 *  @property {function} toggle Function for toggling the pop-up
 *  @property {string} mnemonicPhrase Mnemonic phrase used for generating keyfile
 *  @property {string} seed Seed used for generating keyfile
 *  @property {string} password Password for generating keyfile
 *  @property {function} lockCurrent To lock the currently opened keyfile
 */
const mnemonicPhrasePopupPropTypes = {
    toggle: PropTypes.func.isRequired,
    mnemonicPhrase: PropTypes.string.isRequired,
    seed: PropTypes.string.isRequired,
    password: PropTypes.string.isRequired,
    lockCurrent: PropTypes.func.isRequired
}

/**
 * Pop-up to display the mnemonic phrase and information about the mnemonic phrase.
 * Asks the user to write down the phrase and re-type in the phrase.
 */
export class MnemonicPhrasePopup extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            /**
             * Steps:
             *  "firstInfoPage" - warns user to make sure no one can see their screen,
             *  "showMnemonicPhrase" - shows phrase to write down
             *  "confirmMnemonicPhrase" - asks user to type in mnemonic phrase to confirm.
             */
            currentStep: "firstInfoPage",
            confirmed: false,
            showError: false,
            //the array of words that are shown in the input box (which the user has already clicked on).
            mnemonicPhraseArray: [],
            //the array of words shown below the input box for the user to click on:
            bubbleArray: this.shuffleMnemonicPhrase(),
            tryingToCreateKeyfile: false
        }
        this.toggleCheckBox = this.toggleCheckBox.bind(this);
        this.showMnemonicPhrase = this.showMnemonicPhrase.bind(this);
        this.confirmMnemonicPhrase = this.confirmMnemonicPhrase.bind(this);
        this.confirmCreateKeyfile = this.confirmCreateKeyfile.bind(this);
    }

    /**
     * The initial page that is rendered to display information about the mnemonic phrase.
     * @returns {JSX.Element} the React element for the "firstInfoPage"
     */
    renderFirstInfoPage() {
        return (<div>
            On the next page, you will be shown a mnemonic phrase that will be used to recover your new keyfile.
            Please write down the phrase and save it in a secure place.
            <div className={"bold-phrase"}> Your mnemonic phrase is the only way to recover this keyfile if your computer is lost, broken, or stolen. </div>

            {/* Displays the check box: */}
            <div style={{ marginBottom: "20px"}}>
                {this.state.confirmed
                    ? <img src={selectedBox} className={"selection-box"} alt={"selected-box"} onClick={this.toggleCheckBox}/>
                    : <img src={unselectedBox} className="selection-box" alt="unselected-box" onClick={this.toggleCheckBox}/>
                }
                Before clicking next, please confirm that there is no one that can currently see your screen.
            </div>

            {/* Disables or enables the "Next" button depending on if the checkbox was checked. */}
            {this.state.confirmed
                ? <button className={"blue-square-button"} onClick={this.showMnemonicPhrase}> Next </button>
                : <button className={"blue-square-button"} style={{backgroundColor: "#CCCCCC"}}> Next </button>
            }
        </div>);
    }

    /** Toggles the checkbox - to confirm that the user understands the importance of the mnemonic phrase. */
    toggleCheckBox() {
        this.setState({confirmed: !this.state.confirmed});
    }

    /** Changes the state's currentStep to "showMnemonicPhrase" */
    showMnemonicPhrase() {
        this.setState({
            currentStep: "showMnemonicPhrase",
            confirmed: false
        });
    }

    /**
     * Renders the page for the "showMnemonicPhrase". Asks the user to write down the shown mnemonic phrase.
     * @returns {JSX.Element} the React Element for the page to show the mnemonic phrase.
     */
    renderMnemonicPhrasePage() {
        return (
            <div>
                Please write down the following phrase and keep it in a safe place.
                <div className={"mnemonic-phrase-box"}>
                    {this.props.mnemonicPhrase}
                </div>
                <button className={"blue-square-button"} style={{width: "400px", float: "left", marginLeft: "70px"}}
                        onClick={this.confirmMnemonicPhrase}>
                    I have written down the exact phrase.
                </button>
            </div>
        );
    }

    /** Changes the state's currentStep to "confirmMnemonicPhrase" */
    confirmMnemonicPhrase() {
        this.setState({currentStep: "confirmMnemonicPhrase"})
    }

    /**
     * Renders the "confirmMnemonicPhrase" page. Asks the user to click on the words to re-create their mnemonic phrase.
     * @returns {JSX.Element} the page as React Element.
     */
    renderConfirmMnemonicPhrasePage() {
        if (this.state.tryingToCreateKeyfile) {
            return (
                <div>
                    <ImSpinner3 style={{animation: "rotation 2s infinite linear", marginRight: "5px"}}/>
                    Creating key file...
                </div>
            )
        } else {
            return (
                <div>
                    Please enter your mnemonic phrase below:

                    {/* Input box that displays the already chosen words: */}
                    <div className={"input-style"} style={{width: "400px", height: "100px", padding: "10px"}}>
                        {(this.state.mnemonicPhraseArray.length < 1)
                            ? "Click on words below to form your mnemonic phrase"
                            : this.renderChosenWordBubbles()
                        }
                    </div>

                    {/* Displays the list of words to choose from: */}
                    <div style={{width: "400px", marginBottom: "10px"}}>
                        {this.state.bubbleArray.map(word =>
                            <button className={"mnemonic-word-bubble"} onClick={() => this.addWordToInput(word)}>
                                {word}
                            </button>
                        )}
                    </div>

                    {/* Displays error message: */}
                    {(this.state.showError)
                        ? <span style={{color: "red", fontSize: "12px", display: "block"}}>
                            The phrase you entered does not match your mnemonic phrase. </span>
                        : null
                    }

                    {/* Button to confirm correct mnemonc phrase: */}
                    <button className={"blue-square-button"} style={{float: "none"}} onClick={this.confirmCreateKeyfile}>
                        Confirm
                    </button>
                </div>
            );
        }
    }


    /**
     * Shuffles the mnemonic phrase in a random order.
     * For the user to "un-shuffle" and put the mnemonic phrase in the correct order.
     * @returns {string[]} the mnemonic phrase as a list of scrambled words.
     */
    shuffleMnemonicPhrase() {
        let phrase = this.props.mnemonicPhrase.split(" ");
        for (let i = phrase.length-1; i > 0; i--) {
            const j = Math.floor(Math.random() * (i+1));
            [phrase[i], phrase[j]] = [phrase[j], phrase[i]]
        }
        return phrase;
    }

    /**
     * Function called when the user clicks on a word bubble to add to the input box.
     * @param {string} word - the word that was clicked on to add to the input box.
     */
    addWordToInput(word) {
        this.setState({
            mnemonicPhraseArray: this.state.mnemonicPhraseArray.concat(word),
            bubbleArray: this.state.bubbleArray.filter(item => item !== word)
        });
    }

    /**
     * Function called when the user clicks the "x" button on a word bubble within the input box.
     * Removes the given word from the input box.
     * @param {string} word - the word to be removed.
     */
    removeWordFromInput(word) {
        let array = this.state.mnemonicPhraseArray
        const index = this.state.mnemonicPhraseArray.indexOf(word)
        array.splice(index, 1)

        //removes word from "mnemonicPhraseArray" and adds it back to "bubbleArray"
        this.setState({
            mnemonicPhraseArray: array,
            bubbleArray: this.state.bubbleArray.concat(word)
        });
    }

    /**
     * Renders the words (as bubbles) within the input box, representing the words that the user has already clicked on.
     * @returns {JSX.Element[]} the list of words already chosen displayed as bubbles.
     */
    renderChosenWordBubbles() {
        return this.state.mnemonicPhraseArray.map(word =>
            <div className={"mnemonic-word-bubble"} style={{padding: "3px 0px 3px 5px", float: "left"}}>
                {word}
                <button className={"word-close"} onClick={() => this.removeWordFromInput(word)}> x </button>
            </div>
        )
    }

    /**
     * Checks if the correct mnemonic phrase was inputted and sends HTTP post request to generate the keyfile.
     * @returns {Promise<void>} the promise of the api response
     */
    async confirmCreateKeyfile() {
        if (this.state.mnemonicPhraseArray.join(" ") !== this.props.mnemonicPhrase) {
            this.setState({showError: true});
        }else {
            this.setState({tryingToCreateKeyfile: true})
            const params = {
                "password": this.props.password,
                "seed": this.props.seed
            }
            const response = await createAndSendRequest("wallet_generateKeyfile", params);
            if (response.data.result) {
                await this.props.lockCurrent();
                this.setState({tryingToCreateKeyfile: false})
                this.props.toggle();
            }
        }
    }

    render() {
        let popupPage;
        if (this.state.currentStep === "firstInfoPage") {
            popupPage = this.renderFirstInfoPage();
        } else if (this.state.currentStep === "showMnemonicPhrase") {
            popupPage = this.renderMnemonicPhrasePage();
        } else if (this.state.currentStep === "confirmMnemonicPhrase") {
            popupPage = this.renderConfirmMnemonicPhrasePage();
        }
        return (
            <div className={"new-keyfile-popup"}>
                <CloseButton handleClose={this.props.toggle}/>
                <h1> Mnemonic Phrase </h1>
                {popupPage}
            </div>
        )
    }
}

MnemonicPhrasePopup.propTypes = mnemonicPhrasePopupPropTypes