import React from "react";
import PropTypes from "prop-types";
import {Dropdown} from "../Dropdown";
import {communicationOptionsMap} from "modules/dropdownOptions";
import {createAndSendRequest} from "modules/apiRequests";
import {Popup} from "../Popup";
import {HttpOptions} from "./HttpOptions";

/**
 * Props for {@link CommunicationSettings}
 * @property {string} apiKey the current api key to communicate with Bifrost through Http
 * @property {function} updateData function to update the data after changing the settings
 * @property {string} communicationMode the current communication mode
 */
const communicationSettingsPropTypes = {
    communicationMode: PropTypes.string.isRequired,
    apiKey: PropTypes.string.isRequired,
    updateData: PropTypes.func.isRequired

}

/**
 * Component within the {@link SettingsPage} to allow the user to view and modify the communication mode between bifrost.
 */
export class CommunicationSettings extends React.Component {
    constructor(props) {
        super(props);
        this.state={
            showHttpOptions: false,
            showMoreInfo: false
        }
        this.changeCommunicationMode = this.changeCommunicationMode.bind(this);
        this.toggleHttpOptions = this.toggleHttpOptions.bind(this);
        this.toggleMoreInfo = this.toggleMoreInfo.bind(this);
    }

    /**
     * Sends HTTP post request to change the communication mode
     * @param newMode - the new mode ("useAkka" or "useHttp")
     */
    async changeCommunicationMode(newMode) {
        await createAndSendRequest("wallet_changeCommunicationMode",
            {"mode": communicationOptionsMap.get(newMode)});
        await this.props.updateData();
    }

    /** Open and closes the {@link HttpOptions} pop-up */
    toggleHttpOptions() {
        this.setState({showHttpOptions: !this.state.showHttpOptions});
    }

    toggleMoreInfo() {
        this.setState({showMoreInfo: !this.state.showMoreInfo});
    }

    renderMoreInfo() {
        return (
            <div>
                <div> {"Using the dropdown below, you can choose which form of communication you want Gjallarhorn " +
                "(the backend for this wallet application) to use to communicate with Bifrost " +
                "(the client for the Topl blockchain protocol)."}
                </div>
                <div style={{marginTop: "20px"}}>
                    The mode of communication options are:
                    <ul style={{listStyleType: "none"}}>
                        <li style={{margin: "15px"}}>
                            <b> Akka : </b>
                            {"Gjallarhorn will use Akka Actors to communicate with Bifrost's remote Akka " +
                            "actor. More information about Akka actors can be found at: "}
                            <a href={"https://doc.akka.io/docs/akka/current/typed/actors.html"}>
                                https://doc.akka.io/docs/akka/current/typed/actors.html
                            </a>
                        </li>
                        <li style={{margin: "15px"}}>
                            <b> Http : </b>
                            Gjallarhorn will send HTTP Requests to communicate with Bifrost through its API routes.
                        </li>
                    </ul>
                </div>
            </div>
        );
    }

    render() {
        let infoButton = "More info"
        if (this.state.showMoreInfo) {
            infoButton = "Less info"
        }

        return (
            <div>
                <div style={{paddingBottom: "15px"}}>
                    Mode to communicate with Bifrost:
                    <button className={"text-button"} onClick={this.toggleMoreInfo}>
                        {infoButton}
                    </button>
                </div>
                {this.state.showMoreInfo ? this.renderMoreInfo() : null}
                <Dropdown options={Array.from(communicationOptionsMap.keys())}
                          currentlySelected={this.props.communicationMode}
                          switchSelection={this.changeCommunicationMode} title={"Communication Mode"}
                />
                {this.props.communicationMode === "Http"
                    ? <button className={"text-button"} onClick={this.toggleHttpOptions}> Http Options </button>
                    : null
                }
                {this.state.showHttpOptions
                    ? <Popup page={<HttpOptions apiKey={this.props.apiKey} updateData={this.props.updateData}/>}
                        toggle={this.toggleHttpOptions}/>
                    : null
                }

            </div>
        );
    }
}

CommunicationSettings.propTypes = communicationSettingsPropTypes