import React from "react";
import PropTypes from "prop-types";

/**
 * Props for {@link ErrorPopup}
 *  @property {string} message The error message
 */
const errorPopupPropTypes = {
    message: PropTypes.string.isRequired
}

/** Pop-up to display error messages */
export class ErrorPopup extends React.Component {

    render() {
        return (
            <div>
                <h1 style={{color: "red"}}> Error! </h1>
                <p> Error message: </p>
                <p style={{marginLeft: "20px", color: "red"}}>{this.props.message}</p>
            </div>
        )
    }
}

ErrorPopup.propTypes = errorPopupPropTypes