import React from 'react';
import "styles/balances-page/send/ConfirmSendPopup.css"
import {LinkNext} from "grommet-icons/es6";
import PropTypes from "prop-types";
import {CloseButton} from "components/CloseButton";
import {sum} from "modules/sharedFunctions";

/**
 * Props for {@link ConfirmSendPopup}
 *  @property {function} toggle To handle closing and opening the pop-up
 *  @property {string} fromAddress Address that is sending funds
 *  @property {Array.<{address: string, amount: number}>} recipients List of recipients and amount to send.
 *  @property {string} boxType The type of box to send - "asset", "arbit", "poly"
 *  @property {function} sendTx To send online tx request
 */
const confirmSendPopupPropTypes = {
    toggle: PropTypes.func.isRequired,
    fromAddress: PropTypes.string.isRequired,
    recipients: PropTypes.arrayOf(PropTypes.shape({
        address: PropTypes.string,
        amount: PropTypes.number
    })).isRequired,
    boxType: PropTypes.oneOf(["arbit", "poly", "asset"]).isRequired,
    sendTx: PropTypes.func.isRequired
}

/**
 * Pop-up that is displayed when a user clicks the "Next" button to send an online transaction.
 * This pop-up allows the user to confirm sending an online transaction.
 */
export class ConfirmSendPopup extends React.Component {
    constructor(props) {
        super(props);
        this.renderRecipients = this.renderRecipients.bind(this);
    }

    /**
     * Renders the list of recipients with their amounts to send.
     * @returns {JSX.Element} the list of recipients
     */
    renderRecipients() {
        return (
            <div className={"to-box"}>
                {this.props.recipients.map(recipient => {
                    return (
                        <div>
                            <p className={"amount-sent"}> + {recipient.amount} {this.props.boxType} </p>
                            <p style={{fontWeight: "bold"}}> To Address </p>
                            <p style={{wordWrap: "break-word"}}> {recipient.address} </p>
                        </div>
                )})}
            </div>
        );
    }

    render() {
        const totalSent = sum(this.props.recipients.map(recpt => recpt.amount));
        return (
            <div className={"confirm-online-popup"}>
                <div className={"popUp-content"}>
                    <CloseButton handleClose={this.props.toggle}/>

                    <p className={"popup-header"}> Confirm transaction </p>

                    <div className={"boxes"}>
                        <div className={"from-box"}>
                            <p className={"amount-sent"}> - {totalSent} {this.props.boxType} </p>
                            <p style={{fontWeight: "bold"}}>From Address </p>
                            <p style={{wordWrap: "break-word"}}> {this.props.fromAddress} </p>
                        </div>
                        <LinkNext style={{paddingTop: "70px", paddingLeft: "5px"}}/>
                        {this.renderRecipients()}
                    </div>

                    <button className={"blue-square-button"} style={{margin: "10px 30px 20px 0"}} onClick={this.props.sendTx}>
                        Confirm
                    </button>
                </div>
            </div>
        )
    }
}

ConfirmSendPopup.propTypes = confirmSendPopupPropTypes