
/** This module contains a few functions that are shared across multiple classes. */

/**
 * Capitalizes the given word.
 * @param {string} string - the word to be capitalized
 * @returns {string} the newly capitalized word
 */
function capitalize (string) {
    if (typeof string !== 'string') return '';
    return string.charAt(0).toUpperCase() + string.slice(1);
}

/**
 * Given a map and a value, finds the first key with that value.
 * @param {Map<K, V>} map the map to search through
 * @param {V} value the value to look for in the map
 * @returns {K} the first key found with the given value
 */
function getFirstKeyFromValue(map, value) {
    const keys = Array.from(map.keys());
    return keys.filter(key => map.get(key) === value)[0];
}

/**
 * Given a list of recipients (address and amount), check if it is a valid input.
 * @param {Array.<{address: string, amount: number}>} recipients - the list of recipients to be checked.
 * @param {number} currentBalance - the current balance for the current address trying to send the transaction.
 * @returns {boolean} true if the recipients are invalid, false otherwise.
 */
function invalidRecipients(recipients, currentBalance) {
    const addresses = recipients.map(recpt => recpt.address);
    const amounts = recipients.map(recpt => recpt.amount);
    return addresses.some(emptyAddress) || amounts.some(negativeAmount) ||
        insufficientFunds(recipients, currentBalance);
}

/**
 * True if the given address is an empty string.
 * @param {string} address - the address to be checked
 * @returns {boolean} true if the address is an empty string, false otherwise.
 */
function emptyAddress (address) {
    return (address === "");
}

/**
 * Checks if the given amount is negative.
 * @param {number} amount - the amount to be checked
 * @returns {boolean} true if the amount is 0 or less, false otherwise.
 */
function negativeAmount (amount) {
    return (amount <= 0);
}

/**
 * Checks if the amount is greater than the balance.
 * @param {Array.<{address: string, amount: number}>} recipients - the recipients to be checked.
 * @param {number} balance - the balance to check the amount against.
 * @returns {boolean} true if the amount is greater than the balance, false otherwise.
 */
function insufficientFunds (recipients, balance) {
    const amounts = recipients.map(recpt => recpt.amount);
    const totalAmount = sum(amounts);
    return (totalAmount > balance);
}

/**
 * Sums a list of numbers
 * @param {number[]} list
 * @returns {number} the sum of the list
 */
function sum(list) {
    return list.reduce((acc, val) => acc + val, 0);
}

export { capitalize, getFirstKeyFromValue, invalidRecipients, insufficientFunds, sum }