import React from "react";
import PropTypes from "prop-types";
import {GenerateRawTx} from "../GenerateRawTx";
import {ImportSignedTx} from "./ImportSignedTx";
import {ConfirmSendTx} from "./ConfirmSendTx";
import {Popup} from "components/Popup";
import {UnsuccessfulTx} from "../../UnsuccessfulTx";

/**
 * Props for {@link SendOfflineConnected}:
 *  @property {function} postBroadcast used to show confirmation po-up with tx id.
 *  @property {function} toggleSendOffline Toggles between send offline and online
 *  @property {function} toggleSend Toggles the send tab
 *  @property {string} fromAddress The address of the currently unlocked keyfile
 *  @property {Array.<{address: string, amount: number}>} recipients List of recipients and amount to send.
 *  @property {string} changeAddress
 *  @property {string} consolidationAddress
 *  @property {string} boxType Type of asset to send: "arbit", "poly", or "asset"
 *  @property {number} currentBalance The balance of the currently opened keyfile.
 */
const sendOfflineConnectedPropTypes = {
    postBroadcast: PropTypes.func.isRequired,
    toggleSendOffline: PropTypes.func.isRequired,
    toggleSend: PropTypes.func.isRequired,
    fromAddress: PropTypes.string.isRequired,
    recipients: PropTypes.arrayOf(PropTypes.shape({
        address: PropTypes.string,
        amount: PropTypes.number
    })).isRequired,
    changeAddress: PropTypes.string.isRequired,
    consolidationAddress: PropTypes.string.isRequired,
    boxType: PropTypes.oneOf(["arbit", "poly", "asset"]).isRequired,
    currentBalance: PropTypes.number.isRequired
}

/**
 * This class manages the send offline process when connected to bifrost.
 * "txStep" keeps track of the current step in the process. Valid steps are:
 *      - generateTx {@link GenerateRawTx}
 *      - importSignedTx {@link ImportSignedTx}
 *      - sendTx {@link ConfirmSendTx}
 */
export class SendOfflineConnected extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            txStep: "generateTx",
            signedTx: "",
            showUnsuccessfulPopup: false,
            errorMsg: ""
        }
        this.changeStep = this.changeStep.bind(this);
        this.saveImportedSignedTx = this.saveImportedSignedTx.bind(this);
        this.postBroadcastAttempt = this.postBroadcastAttempt.bind(this);
        this.unsuccessfulTxToggle = this.unsuccessfulTxToggle.bind(this);
    }

    /**
     * Changes the current step in the send offline process
     * @param {string} newStep - the name of the step to change to.
     */
    changeStep(newStep) {
        this.setState({txStep: newStep});
    }

    /**
     * Save the full signed transaction to state and change step to "sendTx" to broadcast the tx.
     * @param {Object} data - the fully signed transaction
     */
    saveImportedSignedTx(data) {
        this.setState({signedTx: data})
        this.changeStep("sendTx")
    }

    /**
     * Function called after receiving API response from broadcast request.
     * Checks response for errors and continues to show confirmation if successful.
     * @param {Object} response - the API response from the broadcast request.
     */
    postBroadcastAttempt(response) {
        if (JSON.stringify(response.data.result).includes("Error")) {
            this.changeStep("unsuccessfulTx");
            this.setState({errorMsg: response.data.result});
            this.unsuccessfulTxToggle();
        }else {
            this.changeStep("successfulTx");
            this.props.postBroadcast(response.data.result["txId"]);
        }
    }

    /** Open and closes unsuccessful transaction pop-up. */
    unsuccessfulTxToggle() {
        if (this.state.showUnsuccessfulPopup) {
            this.props.toggleSend()
        }
        this.setState({
            showUnsuccessfulPopup: !this.state.showUnsuccessfulPopup
        })
    }


    render() {
        let render;

        if (this.state.txStep === "generateTx") {
            render = <GenerateRawTx toggleSendOffline={this.props.toggleSendOffline}
                                    fromAddress={this.props.fromAddress} changeAddress={this.props.changeAddress}
                                    consolidationAddress={this.props.consolidationAddress}
                                    recipients={this.props.recipients} boxType={this.props.boxType}
                                    changeStep={this.changeStep} online={true} currentBalance={this.props.currentBalance}/>
        }

        else if (this.state.txStep === "importSignedTx") {
            render = <ImportSignedTx saveData={this.saveImportedSignedTx} changeStep={this.changeStep}/>
        } else if (this.state.txStep === "sendTx") {
            render = <ConfirmSendTx signedTx={this.state.signedTx} postBroadcastAttempt={this.postBroadcastAttempt}/>
        }

        return (
            <div>
                {render}
                {this.state.showUnsuccessfulPopup
                    ? <Popup page={<UnsuccessfulTx signedTx={JSON.stringify(this.state.signedTx)}
                                                   errorMsg={this.state.errorMsg}/>}
                             toggle={this.unsuccessfulTxToggle}/>
                    : null
                }
            </div>
        );
    }
}

SendOfflineConnected.propTypes = sendOfflineConnectedPropTypes