import React from "react";
import PropTypes from "prop-types";
import {createAndSendRequest} from "modules/apiRequests";

/**
 * Props for {@link EditKeyfileDir}
 *  @property {string} filePath: string The current file path for the key file directory
 *  @property {function} updateData For updating data after editing keyfile directory
 *  @property {function} toggle To close pop-up
 */
const editKeyfileDirPropTypes = {
    filePath: PropTypes.string.isRequired,
    updateData: PropTypes.func.isRequired,
    toggle: PropTypes.func.isRequired
}

/** Popup to edit the key file directory (where the private keys are saved). Displayed from {@link SettingsPage} */
export class EditKeyfileDir extends React.Component {
    constructor(props) {
        super(props);
        this.state={
            path: this.editablePath(this.props.filePath)
        }
        this.handleChange = this.handleChange.bind(this);
        this.changeKeyfileDir = this.changeKeyfileDir.bind(this);

    }

    //TODO: right now can only edit the path from after "/Bifrost/"

    /**
     * Finds the starting index of the editable part of the file path and the ending index of the root part of the file path.
     * Specifically the index following "/Bifrost/"
     * @param {string} filePath - the filepath to find the index for.
     * @returns {number} - the index after "/Bifrost/" in the file path
     */
    getBifrostEndingIndex(filePath) {
        let index = 0
        const root = filePath.indexOf("Bifrost")
        if (root > 0 ){
            index = root + "Bifrost/".length
        }
        return index
    }

    /**
     * Returns the editable part of the filepath
     * @param {string} filePath - the full filepath to find the editable part for.
     * @returns {string} - the editable part of the filepath.
     */
    editablePath(filePath) {
        const end = filePath.lastIndexOf("/")
        return filePath.substring(this.getBifrostEndingIndex(filePath),end)
    }

    /**
     * Returns the file root of the file path.
     * Specifically, the part of the file path that comes before the editable part.
     * @param {string} filePath - the filepath to find the root for.
     * @returns {string} - the non-editable part of the file path.
     */
    fileRoot(filePath) {
        return filePath.substring(0, this.getBifrostEndingIndex(filePath))
    }

    /**
     * Function that handles changes to the "path" input field.
     * @param event - the event that triggers the onChange function.
     */
    handleChange(event) {
        this.setState({[event.target.name]: event.target.value});
    }

    /** Sends HTTP post request to change the key file directory. */
    async changeKeyfileDir() {
        if (this.state.path !== this.editablePath(this.props.filePath)) {
            await createAndSendRequest("wallet_changeKeyfileDir", {"directory": this.state.path});
            await this.props.updateData();
            this.props.toggle();
        }
    }

    render() {
        return (
            <div>
                <div style={{fontSize: "20px", fontWeight: "bold", margin: "15px"}}>
                    Edit current key file directory path:
                </div>

                <div style={{paddingBottom: "10px"}}>
                    Note: this will switch the key file directory and therefore alter which keyfiles are loaded into your wallet.
                    You can still find your other keys at the old file path.
                </div>

                <div style={{margin: "10px", fontSize: "14px"}}>
                    {this.fileRoot(this.props.filePath)}
                    <input type={"text"} name={"path"} value={this.state.path} onChange={this.handleChange}/>
                </div>

                <button className={"blue-square-button"} onClick={this.changeKeyfileDir}
                        style={{float: "none", marginLeft: "35%", marginTop: "20px"}}>
                    Confirm
                </button>
            </div>
        );
    }
}

EditKeyfileDir.propTypes = editKeyfileDirPropTypes