import React from "react";
import QRCode from "qrcode.react";
import PropTypes from "prop-types";
import {CopyButton} from "../CopyButton";
import "styles/keys-page/KeyDetails.css";

/**
 * Props for {@link KeyDetails}:
 * @typedef {Object}
 *  @property {string} address address for current key file
 *  @property {string} name Name of keyfile
 *  @property {boolean} unlocked True, if this key is unlocked
 *  @property {function} lockKeyfile Function to lock this keyfile
 */
const keyDetailsPropTypes = {
    address: PropTypes.string.isRequired,
    name: PropTypes.string.isRequired,
    unlocked: PropTypes.bool.isRequired,
    lockKeyfile: PropTypes.func.isRequired
}

/** Pop-up that display details about a key: the address, QR code, and "lock" button */
export class KeyDetails extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            editMode: false,
            keyfileName: this.props.name
        }
    }

    render() {
        return (
            <div style={{color: "black", boxShadow: "none"}}>
                <h1> {this.state.keyfileName}</h1>

                {/* The public address and copy button: */}
                <p style={{float: "left", fontSize: "15px", marginTop: "10px"}}
                   id={"details-address"}>
                    {this.props.address}
                </p>
                <div style={{float: "left"}}>
                    <CopyButton elementIDToCopy={"details-address"} />
                </div>

                {/* QR code: */}
                <div style={{marginLeft: "40%"}}>
                    <QRCode value={this.props.address}/>
                </div>

                {/* Lock button (if currently unlocked) */}
                {this.props.unlocked
                    ? <button className={"round-blue-button"} style={{position: "relative", left: "38%", marginTop: "20px", width: "150px",
                        fontSize: "18px", height: "40px", borderRadius: "30px"}} onClick={() => this.props.lockKeyfile(this.props.address)}>
                        Lock
                    </button>
                    : null}
            </div>
        )
    }
}

KeyDetails.propTypes = keyDetailsPropTypes