import React from "react";
import {FormClose} from "grommet-icons";
import "styles/balances-page/CloseButton.css";
import PropTypes from "prop-types";

/** Props for {@link CloseButton}
 * @property {function} handleClose Function called to close pop-up
 */
const closeButtonPropTypes = {
    handleClose: PropTypes.func.isRequired
}

/**
 * Close button is a button mostly used for pop-up components ({@link Popup}) to close a tab or pop-up.
 * @example
 * return (<CloseButton handleClose={this.toggle}/>)
 */
export class CloseButton extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            hovered: false
        }
        this.toggleHover = this.toggleHover.bind(this);
    }

    /** Toggles the "hovered" state of the button */
    toggleHover() {
        this.setState({hovered: !this.state.hovered});
    }

    /**
     * Renders a small "x" that changes colors when hovered
     * @returns {JSX.Element}
     */
    render() {
        let closeColor = "";
        if (this.state.hovered) {
            closeColor = "black";
        }

        return (
            <button className={"close"} onClick={this.props.handleClose}
                    onMouseEnter={this.toggleHover} onMouseLeave={this.toggleHover}>
                <FormClose color={closeColor} className={"x-close"}/>
            </button>
        )

    }
}

CloseButton.propTypes = closeButtonPropTypes