import React from 'react';
import PropTypes from "prop-types";
import {StatusWarning} from "grommet-icons";

/**
 * Props for {@link UnsuccessfulTx}:
 *  @property {string} errorMsg The error message received from the api after sending a tx request
 *  @property {string} signedTx The signed transaction attempted to send
 */
const unsuccessfulTxPropTypes = {
    errorMsg: PropTypes.string.isRequired,
    signedTx: PropTypes.string.isRequired
}

/** Pop-up to show error message when there's an unsuccessful tx, post broadcast or online transfer. */
export class UnsuccessfulTx extends React.Component {
    render() {
        return (
            <div>
                <StatusWarning size="large" color="red" style={{float: "left", paddingLeft: "10px", paddingRight: "10px"}}/>
                <h1> Transaction Error </h1>
                <span style={{display: "block"}}>
                    The attempted transaction was not broadcasted due to:
                </span>
                <div style={{color: "red"}}> {this.props.errorMsg} </div>
                <div style={{fontWeight: "bold", padding: "10px"}}>  Attempted tx: </div>
                <div style={{paddingLeft: "40px"}}> {this.props.signedTx} </div>
            </div>
        )
    }
}

UnsuccessfulTx.propTypes = unsuccessfulTxPropTypes