import React from "react";
import {Copy} from "grommet-icons";
import ReactTooltip from "react-tooltip";
import PropTypes from "prop-types";
import "styles/full-page/CopyButton.css";

/**
 * Props for {@link CopyButton}
 *  @property {string} elementIdToCopy The element id that should be copied when the button is clicked on.
 */
const copyButtonPropTypes = {
    elementIDToCopy: PropTypes.string.isRequired
}

/** This button is used to copy values to user's clipboard */
export class CopyButton extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            copied: false
        }
        this.copy = this.copy.bind(this);
    }

    /** Copies this.props.elementIDToCopy to the user's clipboard */
    copy() {
        const range = document.createRange();
        range.selectNode(document.getElementById(this.props.elementIDToCopy));
        window.getSelection().removeAllRanges();
        window.getSelection().addRange(range);
        document.execCommand("copy")
        window.getSelection().removeAllRanges();
        this.setState({copied: true});
    }

    render() {

        //Sets the message for the tooltip for the copy button.
        let copyMessage;
        if (this.state.copied) {
            copyMessage = "Copied!";
        }else{
            copyMessage = "Copy to clipboard";
        }

        return (
            <span>
                <button data-tip="" data-for={"copyTip"} className={"copy-button"} onClick={this.copy}>
                    <Copy color={"white"}/>
                </button>
                <ReactTooltip className={"copy-tooltip"} id={"copyTip"} place={"bottom"} effect={"solid"}>
                    {copyMessage}
                </ReactTooltip>
            </span>
        );
    }
}

CopyButton.propTypes = copyButtonPropTypes
