import React from "react"
import PropTypes from "prop-types";
import {BiRefresh, ImSpinner3} from "react-icons/all";
import 'styles/balances-page/WalletSummary.css';
import 'styles/ToplButtons.css';
import {Balance} from "./Balance";
import {AssetTable} from "./assets/AssetTable";
import {AddressBox} from "./AddressBox";

/**
 * Props for {@link MyBalancesPage}
 * @property {string} public address The public address of the keyfile currently unlocked.
 * @property {Array.<{boxType: string, balance: number}>} assets Poly and arbit balances
 * @property {Array.<{code: string, balance: number}>} assetList Asset balances
 * @property {boolean} online True if currently connected to bifrost
 * @property {boolean} retrievingData True if still retrieving data, else false
 * @property {boolean} allLocked True if all of the keys are locked
 * @property {function} openKeys Function to open keys page.
 * @property {function} updateData Function that retrieves data from back-end
 */
const myBalancesPagePropTypes = {
    publicAddress: PropTypes.string,
    assets: PropTypes.arrayOf(PropTypes.shape({
        boxType: PropTypes.oneOf(["arbit", "poly"]),
        balance: PropTypes.number
    })),
    assetsList: PropTypes.arrayOf(PropTypes.shape({
        code: PropTypes.string,
        balance: PropTypes.number
    })),
    online: PropTypes.bool.isRequired,
    retrievingData: PropTypes.bool.isRequired,
    allLocked: PropTypes.bool,
    openKeys: PropTypes.func,
    updateData: PropTypes.func.isRequired
}

/** This component represents the "My Balances" page */
export class MyBalancesPage extends React.Component{

    constructor(props) {
        super(props);
        this.state={
            showRecentTxMsg: false
        }
        this.showRecentTxMsg = this.showRecentTxMsg.bind(this);
        this.refresh = this.refresh.bind(this);
    }

    /**
     * Render the list of balances (Polys and Arbits)
     * @returns {JSX.Element} list of {@link Balance} components for each asset.
     */
    renderBalanceList() {
        if (this.props.assets) {
            return this.props.assets.map(asset =>
                <Balance publicAddress={this.props.publicAddress} balance={asset.balance} boxType={asset.boxType}
                         online={this.props.online} showRecentTxMsg={this.showRecentTxMsg}/>
            )
        } else {
            //if there are no assets, just displays poly and arbit balances of 0:
            return (
                <div>
                    <Balance publicAddress={this.props.publicAddress} balance={0} boxType={"poly"}
                             online={this.props.online} showRecentTxMsg={this.showRecentTxMsg}
                    />
                    <Balance publicAddress={this.props.publicAddress} balance={0} boxType={"arbit"}
                             online={this.props.online} showRecentTxMsg={this.showRecentTxMsg}
                    />
                </div>);
        }
    }

    /** Toggles displaying the "recent transaction" message */
    showRecentTxMsg() {
        this.setState({showRecentTxMsg: !this.state.showRecentTxMsg});
    }

    /**
     * Function called when user clicks the refresh button (next to "Balances" and "Other Assets")
     * Updates the data and gets rid of the "recent transaction" message.
     */
    refresh() {
        if (this.state.showRecentTxMsg) {
            this.setState({showRecentTxMsg: false})
        }
        this.props.updateData();
    }


    render() {
        //Initializes the message to show depending on the state:
        let msg;

        //If still retrieving data from back-end:
        if (this.props.retrievingData) {
            msg = <div>
                <ImSpinner3 style={{animation: "rotation 2s infinite linear"}}/>
                Loading your keyfiles...
            </div>
        }

        //If all of the keys are currently locked:
        else if (this.props.allLocked) {
            msg = <div>
                All of your keys are currently locked. You can unlock a key on the
                <button className={"text-button"} onClick={this.props.openKeys}> keys page </button>.
            </div>

        }

        //If the user is offline (disconnected from Bifrost), but has keys:
        else if (this.props.publicAddress != null && !this.props.online) {
            msg = <div style={{color: "red"}}>
                You're not connected to bifrost, so you're balances may not be up to date.
            </div>
        }

        //The user does not have any keys:
        else if (!this.props.publicAddress) {
            msg = <div>
                You currently do not have any keys. You can import or generate a new key on the
                <button className={"text-button"} onClick={this.props.openKeys}> keys page </button>.
            </div>
        }

        return (
                <div className={"outer-gray"}>

                    {/* Your public address: */}
                    <div className={"inner-box"}>
                        <AddressBox publicAddress={this.props.publicAddress}/>
                    </div>

                    {/* Balances: */}
                    <div className={"inner-box"}>
                        <div className={"table-title"}>
                            Balances
                            <button className={"refresh-button"} onClick={this.refresh}>
                                <BiRefresh className={"refresh-symbol"}/>
                            </button>
                            {this.state.showRecentTxMsg
                                ? <span style={{fontSize: "15px", color: "red"}}> Recently sent transactions. Balances may be out of date. </span>
                                : null
                            }
                        </div>
                        {this.renderBalanceList()}
                        {msg}
                    </div>

                    {/* Other Assets: */}
                    <div className={"inner-box"}>
                        <div className={"table-title"}>
                            Other Assets
                            <button className={"refresh-button"} onClick={this.refresh}>
                                <BiRefresh className={"refresh-symbol"}/>
                            </button>
                            {this.state.showRecentTxMsg ?
                                <span style={{fontSize: "15px", color: "red"}}> Recently sent transactions. Assets information may be out of date. </span>
                                : null}
                        </div>
                        <AssetTable assetsList={this.props.assetsList} currentAddress={this.props.publicAddress}
                                    online={this.props.online} retrievingData={this.props.retrievingData}
                                    showRecentTxMsg={this.showRecentTxMsg}/>
                    </div>
                </div>
            )
    }
}

MyBalancesPage.propTypes = myBalancesPagePropTypes


