import React from "react";
import PropTypes from "prop-types";
import "styles/balances-page/send/SendOffline.css";
import "styles/balances-page/send/SendPopup.css";
import "styles/ToplButtons.css";
import {ImportRawTx} from "./ImportRawTx";
import {GenerateRawTx} from "../GenerateRawTx";
import {CopyButton} from "components/CopyButton";

/**
 * Props for {@link SendOfflineDisconnected}:
 *  @property {string} boxType Type of asset to send: "arbit", "poly", or "asset"
 *  @property {function} toggleSendOffline Toggles between send offline and online
 *  @property {string} fromAddress The address of the currently unlocked keyfile
 *  @property {Array.<{address: string, amount: number}>} recipients List of recipients and amount to send.
 *  @property {string} changeAddress
 *  @property {string} consolidationAddress
 *  @property {number} currentBalance The balance of the currently opened keyfile.
 */
const sendOfflineDisconnectedPropTypes = {
    boxType: PropTypes.oneOf(["arbit", "poly", "asset"]).isRequired,
    toggleSendOffline: PropTypes.func.isRequired,
    fromAddress: PropTypes.string.isRequired,
    recipients: PropTypes.arrayOf(PropTypes.shape({
        address: PropTypes.string,
        amount: PropTypes.number
    })).isRequired,
    changeAddress: PropTypes.string.isRequired,
    consolidationAddress: PropTypes.string.isRequired,
    currentBalance: PropTypes.number.isRequired
}

/**
 * This class manages the send offline process when disconnected from bifrost.
 * "txStep" keeps track of the current step in the process. Valid steps are:
 *      - intialpage {@link ImportRawTx}
 *      - generateTx {@link GenerateRawTx}
 *      - exportSignedTx
 *      - exportSignature
 */
export class SendOfflineDisconnected extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            txStep: "initialPage",
            signedTx: "",
            txId: null,
        }
        this.changeStep = this.changeStep.bind(this);
        this.saveSignedTx = this.saveSignedTx.bind(this);
    }


    /**
     * Changes the current step in the send offline process
     * @param {string} newStep - the name of the step to change to.
     */
    changeStep(newStep) {
        this.setState({txStep: newStep});
    }

    /**
     * This function is called after signing a raw transaction.
     * Saves the fully signed transaction to state and changes the step.
     * @param {Object} signedTx - the fully signed transaction
     * @param {string} step - the step to change to
     */
    saveSignedTx(signedTx, step) {
        this.setState({
            signedTx: signedTx,
            txId: signedTx["txId"]
        })
        this.changeStep(step);
    }


    render() {
        let render;
        if (this.state.txStep === "initialPage") {
            render = <ImportRawTx saveSignedTx={this.saveSignedTx} fromAddress={this.props.fromAddress}
                                  changeStep={this.changeStep}/>
        }
        else if (this.state.txStep === "generateTx") {
            render = <GenerateRawTx toggleSendOffline={this.props.toggleSendOffline}
                                    fromAddress={this.props.fromAddress} changeAddress={this.props.changeAddress}
                                    consolidationAddress={this.props.consolidationAddress}
                                    recipients={this.props.recipients} boxType={this.props.boxType}
                                    changeStep={this.changeStep} online={false} currentBalance={this.props.currentBalance}/>
        } else {
            let title, body, data;
            //Export signed tx:
            if (this.state.txStep === "exportSignedTx") {
                data = "data:text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(this.state.signedTx));
                title = "Signed Transaction ID";
                body =
                    <div>
                        <div className={"sign-tx-gray-box"} id={"signedTransaction"}>
                            {this.state.txId}
                        </div>
                        <div style={{float: "left", marginRight: "10px"}}>
                            <CopyButton elementIDToCopy={"signedTransaction"}/>
                        </div>
                        <button className={"blue-square-button"} style={{float: "none", fontSize: "16px"}}>
                            <a href={data} download={"topl-signed-tx.json"} style={{color: "white"}}>
                                Download JSON
                            </a>
                        </button>
                    </div>
            }
            //Export signatures:
            else if (this.state.txStep === "exportSignature") {
                data = "data:text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(this.state.signedTx["signatures"]));
                title = "Signature(s)";
                body =
                    <div style={{display: "inline-block", margin: "10px"}}>
                        <button className={"blue-square-button"} style={{float: "none", fontSize: "16px"}}>
                            <a href={data} download={"topl-signatures.json"} style={{color: "white"}}> Download JSON </a>
                        </button>
                    </div>
            }
            render = (
                <div>
                    <h1 style={{paddingLeft: "10px", fontSize: "20px", display: "inline"}}> {title} </h1>
                    {body}
                </div>
            );
        }
        return <div> {render} </div>
    }
}

SendOfflineDisconnected.propTypes = sendOfflineDisconnectedPropTypes