import React from 'react';
import PropTypes from "prop-types";
import {FormAdd} from "grommet-icons";
import {CreateAssetPopup} from "./CreateAssetPopup";
import {AssetRow} from "./AssetRow";
import "styles/balances-page/assets/AssetTable.css"
import "styles/ToplButtons.css";
import {capitalize} from "modules/sharedFunctions";

/**
 * Props for {@link AssetTable}
 * @property {boolean} retrievingData True, if still waiting on data from backend
 * @property {function} showRecentTxMsg Function to show message after a recent tx
 * @property {Array.<{code: string, balance: number}>} assetsList List of asset codes and balances
 * @property {string} currentAddress Address of currently unlocked key file
 * @property {boolean} online True, if currently connected to bifrost
 */
const assetTablePropTypes = {
    retrievingData: PropTypes.bool.isRequired,
    showRecentTxMsg: PropTypes.func.isRequired,
    assetsList: PropTypes.arrayOf(PropTypes.shape({
        code: PropTypes.string,
        balance: PropTypes.number,
    })),
    currentAddress: PropTypes.string,
    online: PropTypes.bool
}

/** The asset table displays all of the assets for a given key in a table format with the asset code and balance. */
export class AssetTable extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showCreateAssetPopup: false,
            sendOrReceiveOpen: false
        }
        this.toggleCreateAssetPopup = this.toggleCreateAssetPopup.bind(this);
    }

    /**
     * Creates an asset row for each asset
     * @returns {JSX.Element[]} a list of {@link AssetRow}s
     */
    renderTable() {
        return this.props.assetsList.map((asset) => {
            const{code, balance} = asset;
            return <AssetRow balance={balance} code={code} currentAddress={this.props.currentAddress}
                             online={this.props.online}  showRecentTxMsg={this.props.showRecentTxMsg}/>
        })
    }

    /**
     * Creates the table header given the keys from the asset objects.
     * The header should contain: code and balance
     * @returns {JSX.Element[]} a list of the headers for the table
     */
    renderTableHeader() {
        let header = Object.keys(this.props.assetsList[0]);
        return header.map((key) => {
                return <th className={"table-header"}>{capitalize(key)}</th>
        })
    }

    /** Open and closes {@link CreateAssetPopup} */
    toggleCreateAssetPopup() {
        this.setState({showCreateAssetPopup: !this.state.showCreateAssetPopup});
    }

    /**
     * Creates asset table for non-empty list of assets
     * @returns {JSX.Element} the asset table element
     */
    renderNonemptyAssetTable() {
        return (
            <div style={{overflow: "auto"}}>
                <table className={"other-assets"}>
                    <thead> <tr>
                        {this.renderTableHeader()} <th className={"table-header-actions"}> Actions </th>
                    </tr> </thead>
                    <tbody>{this.renderTable()}</tbody>
                </table>
                {this.state.showCreateAssetPopup
                    ? <CreateAssetPopup  currentAddress={this.props.currentAddress} toggle={this.toggleCreateAssetPopup}
                                         showRecentTxMsg={this.props.showRecentTxMsg}/>
                    : <button className={"round-blue-button"} style={{left: "36%", width: "156px", position: "relative", marginTop: "20px"}} onClick={this.toggleCreateAssetPopup}>
                        <FormAdd color={"white"} thickness={"large"} style={{height: "25px", verticalAlign: "middle"}}/>
                        Create Asset
                    </button>}
            </div>
        );
    }

    /**
     * Creates a message for an empty list of assets
     * @returns {JSX.Element} the element for the message
     */
    renderNoAssetsMsg() {
        return (
            <div>
                <span style={{paddingLeft: "30%"}}> You currently do not have any assets. </span>
                <div>
                    {this.state.showCreateAssetPopup
                        ? <CreateAssetPopup currentAddress={this.props.currentAddress} toggle={this.toggleCreateAssetPopup}
                                            showRecentTxMsg={this.props.showRecentTxMsg}/>
                        : <button className={"round-blue-button"} style={{left: "36%", width: "156px", position: "relative", marginTop: "20px"}} onClick={this.toggleCreateAssetPopup}>
                            <FormAdd color={"white"} thickness={"large"} style={{height: "25px", verticalAlign: "middle"}}/>
                            Create Asset
                        </button>}
                </div>
            </div>
        );
    }

    /**
     * Creates an empty list of assets
     * @param {JSX.Element} msg - the message to display why the table is empty.
     * @returns {JSX.Element} an empty asset table
     */
    renderEmptyAssetTable(msg) {
        return (
            <div style={{overflow: "auto", fontSize: "14px"}}>
                <table className={"other-assets"}>
                    <thead>
                    <tr>
                        <th className={"table-header"}> Code </th>
                        <th className={"table-header"}> Balance </th>
                        <th className={"table-header"}> Actions </th>
                    </tr>
                    </thead>
                </table>
                {msg}
            </div>
        );
    }

    render() {
        //non-empty list of assets:
        if (this.props.assetsList.length > 0) {
            return this.renderNonemptyAssetTable();
        }

        //still loading OR offline OR no assets:
        else{
            let msg;

            // still retrieving data for assets
            if (this.props.retrievingData) {
                msg = <span> Loading assets... </span>
            }

            // offline so can't get updated list of assets
            else if (!this.props.online) {
                msg = <span style={{color: "red", paddingLeft: "25%"}}>
                    You're not connected to bifrost, so you're assets cannot be retrieved.
                </span>
            }

            // current keyfile has no assets
            else if (this.props.currentAddress) {
                msg = this.renderNoAssetsMsg();
            }

            return this.renderEmptyAssetTable(msg);
        }
    }
}

AssetTable.propTypes = assetTablePropTypes