import React from "react";
import "styles/balances-page/ReceivePopup.css"
import PropTypes from "prop-types";
import {CopyButton} from "../CopyButton";

/**
 * Props for {@link AddressBox}
 * @property {string} publicAddress The address of the keyfile currently opened.
 */
const addressBoxPropTypes = {
    publicAddress: PropTypes.string.isRequired,
}

/**
 * The component that displays the current address at the top of the "My Balances" page.
 */
export class AddressBox extends React.Component {
    render() {
        return (
            <div>
                <div className={"table-title"} style={{paddingBottom: "0"}}> Your public address: </div>
                <p style={{whiteSpace: "nowrap"}}>
                    <span className={"address-field"} id={"address"}> {this.props.publicAddress} </span>
                    <CopyButton  elementIDToCopy={"address"}/>
                </p>
            </div>
        )
    }
}

AddressBox.propTypes = addressBoxPropTypes