import React from "react";
import PropTypes from 'prop-types';
import {Checkmark, FormDown, FormUp} from "grommet-icons";
import "styles/Dropdown.css";

/**
 * Props for {@link Dropdown}
 * @property {string} currentlySelected The option that is currently selected
 * @property {string} margin (optional) margin for styling the dropdown
 * @property {Array} options the list of options to select from
 * @property {string} title the title for the dropdown list
 * @property {function} switchSelection function called when selecting a new option in the list.
 */
const dropdownPropTypes = {
    title: PropTypes.string.isRequired,
    options: PropTypes.array.isRequired,
    currentlySelected: PropTypes.string.isRequired,
    switchSelection: PropTypes.func.isRequired,
    margin: PropTypes.string
}

/**
 * Dropdown component used to allow the user to select from a set of options.
 * Currently used for the language dropdown in the {@link SettingsPage} and the {@link CommunicationSettings}
 */
export class Dropdown extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            open: false
        }
        this.wrapperRef = React.createRef();
        this.toggleDropdown = this.toggleDropdown.bind(this);
        this.handleClickOutside = this.handleClickOutside.bind(this);
    }

    /**
     * Adds an event listener to listen for mouse clicks when this component mounts.
     * This is used to close the dropdown when the user clicks outside of the dropdown component.
     */
    componentDidMount() {
        document.addEventListener('mousedown', this.handleClickOutside);
    }

    /** Removes the event listener before unmounting the component. */
    componentWillUnmount() {
        document.removeEventListener('mousedown', this.handleClickOutside);
    }

    /**
     * Called when a click occurs. Checks if the click is inside or outside of the component (this.wrapperRef)
     * If the click is outside of the component, closes the dropdown.
     * @param event - the "mousedown" event
     */
    handleClickOutside(event) {
        if (this.wrapperRef && !this.wrapperRef.current.contains(event.target)) {
            this.setState({open: false});
        }
    }

    /** Open and closes the dropdown. */
    toggleDropdown() {
        this.setState({open: !this.state.open});
    }

    /**
     * Renders the dropdown
     * @returns {JSX.Element} - the dropdown as a React Element
     */
    renderDropdown() {
        return (
            <ul className={"dropdown"} style={{margin: this.props.margin}}>
                <h1 className={"dropdown-title"}> {this.props.title} </h1>
                {this.props.options.map(option => {

                    //puts checkmark next to currently selected option:
                    if (option === this.props.currentlySelected) {
                        return (
                            <button className={"dropdown-item"} key={option + "-button"}>
                                {option}
                                <Checkmark color={"black"} style={{float: "right", height: "17px"}}/>
                            </button>
                        )
                    }

                    else {
                        return (
                            <button className={"dropdown-item"} key={option+"-button"}
                                    onClick={() => this.props.switchSelection(option)}>
                                {option}
                            </button>
                        )
                    }
                })}
            </ul>
        )
    }


    render() {
        let arrow;

        //set the arrow styling based on if the dropdown is opened or closed:
        if (this.state.open) {
            arrow = <FormUp color={"#171717"} style={{float: "right"}}/>;
        }else{
            arrow = <FormDown color={"#171717"} style={{float: "right"}}/>;
        }

        return (
            <div ref={this.wrapperRef} style={{display: "inline", position: "relative"}}>
                <button className={"dropdown-button"} onClick={this.toggleDropdown}>
                    {this.props.currentlySelected}
                    {arrow}
                </button>
                {this.state.open ? this.renderDropdown() : null}
            </div>
        );
    }
}

Dropdown.propTypes = dropdownPropTypes