import React from "react";
import PropTypes from "prop-types";
import {Popup} from "components/Popup";
import {ImportTx} from "../ImportTx";

/**
 * Props for {@link ImportSignedTx}
 *  @property {function} saveData Used to save data from imported file
 *  @property {function} changeStep To change the step in the send offline process
 */
const importSignedTxPropTypes = {
    saveData: PropTypes.func.isRequired,
    changeStep: PropTypes.func.isRequired
}

/**
 * Page shown to import a fully signed transaction.
 * Gets displayed when user clicks "Already have signed tx" button in {@link ./GenerateRawTx}.
 */
export class ImportSignedTx extends React.Component {
    constructor(props) {
        super(props);
        this.state={
            //signed tx as an object
            signedTx: null,
            //signed tx as a string for rendering in input box
            signedTxString: "",
            error: false,
            showImportPopup: false
        }
        this.importFile = this.importFile.bind(this);
        this.checkInputAndContinue = this.checkInputAndContinue.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.togglePopup = this.togglePopup.bind(this);
    }

    /**
     * Function to handle changes to input elements: signedTxString
     * @param event - the event that prompted the onChange function
     */
    handleChange(event) {
        this.setState({[event.target.name]: event.target.value});
    }

    /**
     * Function that is called when the "Continue" button is clicked.
     * Checks for a nonempty signedTx, saves the signedTx to the state in {@link ../SendOfflineConnected}
     * and changes step to "sendTx" to broadcast the tx.
     */
    checkInputAndContinue() {
        if (this.state.signedTxString === "") {
            this.setState({error: true});
        }else{
            this.props.saveData(this.state.signedTx);
        }
    }

    /** Open and closes the import pop-up */
    togglePopup() {
        this.setState({showImportPopup: !this.state.showImportPopup})
    }

    /**
     * Saves the parsed data from the uploaded file to state and closes the pop-up
     * @param {Object} data - the data from the uploaded file (should be a fully signed tx)
     */
    importFile(data) {
        this.setState({
            signedTx: data,
            signedTxString: JSON.stringify(data)
        })
        this.togglePopup();
    }


    render() {
        let textArea = "signed-tx-input";
        if (this.state.error) {
            textArea = "signed-tx-error";
        }
        return(
            <div className={"signed-tx"}>
                Signed Transaction:
                <textarea name={"signedTxString"} value={this.state.signedTxString} className={textArea} onChange={this.handleChange}/>
                {this.state.error
                    ? <span className={"error-message"}> Must import or paste in signed transaction. </span>
                    : null
                }

                <button className={"import-button"} onClick={this.togglePopup}>
                    Import JSON File
                </button>
                <button className={"blue-square-button"} onClick={this.checkInputAndContinue}>
                    Continue
                </button>
                <button className={"text-button"} onClick={()=> this.props.changeStep("generateTx")}
                        style={{float: "right", margin: "10px"}}>
                    Back
                </button>

                {this.state.showImportPopup
                    ? <Popup page={<ImportTx upload={this.importFile} expectedImport={"Signed Transaction"}/>} toggle={this.togglePopup}/>
                    : null
                }
            </div>
        )
    }
}

ImportSignedTx.propTypes = importSignedTxPropTypes