import React from 'react';
import PropTypes from "prop-types";

/**
 * Props for {@link ImportTx}:
 *  @property {function} upload To save data that was imported
 *  @property {string} expectedImport A title for what is being imported - ex: "raw transaction", "signatures"
 */
const importTxPropTypes = {
    upload: PropTypes.func.isRequired,
    expectedImport: PropTypes.string.isRequired
}

/** Component used to select and import files */
export class ImportTx extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            data: null,
            errorMsg: null
        }
        this.handleChange = this.handleChange.bind(this);
        this.uploadFile = this.uploadFile.bind(this);
    }

    /**
     * Function that is called when a file is selected.
     * Attempts to read and parse the file and save the object to this.state.data.
     * @param event - the event that prompted the onChange function
     */
    handleChange(event) {
        if (event.target.files) {
            const file = event.target.files[0];
            const reader = new FileReader()
            reader.readAsText(file)
            reader.onload = () => {
                const text = reader.result
                //parses the text and saves to state
                if (typeof text === "string") {
                    let content
                    try {
                        content = JSON.parse(text);
                        this.setState({data: content});
                    } catch (e) {
                        this.setState({errorMsg: `Error parsing text as json: "${e}"`});
                    }
                } else {
                    this.setState({errorMsg: `The text was of type ${typeof text}. It must be of type string.`});
                }
            };

            //if the reader fails to read the file:
            reader.onerror = function (error) {
               this.setState({errorMsg: `There was an error when reading the file: ${error}"}`});
            }
        }
    }

    /**
     * Function called when the "Upload" button is clicked.
     * Checks for valid data and shows error message or uploads the data.
     */
    uploadFile() {
        if (!this.state.data) {
            if (!this.state.errorMsg) {
                this.setState({errorMsg: "No file was selected to upload."});
            }
        }else {
            this.props.upload(this.state.data)
        }
    }

    render() {
        return (
            <div style={{color: "black"}}>
                <h1> Import {this.props.expectedImport}: </h1>
                <input type={"file"} name={"file"} onChange={this.handleChange}/>
                <button className={"blue-square-button"} onClick={this.uploadFile}
                        style={{width: "200px", position: "absolute", bottom: "0", right: "0"}}>
                    Upload
                </button>
                {this.state.errorMsg ? <div style={{color: "red"}}> {this.state.errorMsg} </div> : null}
            </div>
        )
    }
}

ImportTx.propTypes = importTxPropTypes