import React from 'react';
import PropTypes from "prop-types";
import {Send} from "grommet-icons";
import {SendTab} from "../send/SendTab";
import "styles/balances-page/assets/AssetRow.css";

/**
 * Props for {@link AssetRow}:
 *  @property {number} balance The balance for this asset
 *  @property {string} code The assetCode for this asset
 *  @property {string} currentAddress The address of the currently unlocked keyfile
 *  @property {boolean} online connected to bifrost or not.
 *  @property {function} showRecentTxMsg To show message that a recent transaction has occurred
 */
const assetRowPropTypes = {
    balance: PropTypes.number.isRequired,
    code: PropTypes.string.isRequired,
    currentAddress: PropTypes.string.isRequired,
    online: PropTypes.bool.isRequired,
    showRecentTxMsg: PropTypes.func.isRequired
}

/** This component represents an asset row in the asset table on the balances page */
export class AssetRow extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            sendOpen: false,
        }
        this.toggleSend = this.toggleSend.bind(this);
    }

    /** Toggles the send tab for this asset */
    toggleSend() {
        this.setState({sendOpen: !this.state.sendOpen,});
    }

    /**
     * Creates the send button for this asset row
     * @returns {JSX.Element} the send button within the asset row
     */
    renderSendButton() {
        return <div className={"asset-table-action-buttons"}>
                <button className={"asset-table-action-button"} onClick={this.toggleSend}>
                    <span> Send </span>
                    <Send size="small" style={{verticalAlign: "middle"}}/>
                </button>
            </div>;
    }

    render() {
        return (
            <tr>
                <td className={"asset-cell"}>{this.props.code}</td>
                <td className={"asset-cell"}>{this.props.balance}</td>
                <td> {this.renderSendButton()}</td>
                {this.state.sendOpen
                    ? <div className={"send-popup"}>
                        <h1 style={{fontFamily: "Roboto", fontSize: "18px", paddingLeft: "44px"}}>
                            {this.props.code}: {this.props.balance}
                        </h1>
                        <SendTab boxType={"asset"} fromAddress={this.props.currentAddress}
                                 toggleSend={this.toggleSend} online={this.props.online}
                                 currentBalance={this.props.balance} showRecentTxMsg={this.props.showRecentTxMsg}/>
                    </div>
                    : null}
            </tr>
        )

    }
}

AssetRow.propTypes = assetRowPropTypes