import React from "react";
import PropTypes from "prop-types";
import {createAndSendRequest} from "modules/apiRequests";

/**
 * Props for {@link HttpOptions}
 * @property {string} apiKey the current api key to communicate with Bifrost through Http
 * @property {function} updateData function for updating the data
 */
const httpOptionsPropTypes = {
    apiKey: PropTypes.string.isRequired,
    updateData: PropTypes.func.isRequired
}

export class HttpOptions extends React.Component {
    constructor(props) {
        super(props);
        this.state= {
            apiKey: this.props.apiKey
        }
        this.handleChange = this.handleChange.bind(this);
        this.changeApiKey = this.changeApiKey.bind(this);
    }

    /**
     * Function to handle changes to input elements: signedTxString
     * @param event - the event that prompted the onChange function
     */
    handleChange(event) {
        this.setState({[event.target.name]: event.target.value});
    }

    /** Sends HTTP post request to change api key and updates the data afterwards. */
    async changeApiKey() {
        if (this.state.apiKey !== this.props.apiKey) {
            await createAndSendRequest("wallet_changeApiKey", {"apiKey": this.state.apiKey});
            await this.props.updateData();
        }
    }

    render() {
        //Set the button style based on if the api key has been modified:
        let buttonStyle = "blue-square-button"
        if (this.state.apiKey === this.props.apiKey) {
            buttonStyle = "disabled-square-button"
        }

        return (
            <div>
                <h2> HTTP Options </h2>
                <ul style={{listStyleType: "none"}}>
                    <li> <b> API Key : </b>
                        <input type={"text"} name={"apiKey"} value={this.state.apiKey}
                               onChange={this.handleChange}/>
                    </li>
                </ul>

                <button className={buttonStyle} onClick={this.changeApiKey}>
                    Apply changes
                </button>
            </div>
        )
    }
}

HttpOptions.propTypes = httpOptionsPropTypes

