import React from "react";
import PropTypes from "prop-types";
import "styles/keys-page/NewKeyfilePopup.css";
import "styles/ToplButtons.css";
import {CloseButton} from "../CloseButton";
import {createAndSendRequest} from "modules/apiRequests";

/**
 * Props for {@link NewKeyfilePopup}
 *     @property {function} toggle For opening/closing popup,
 *     @property {function} saveKeyFileInfo For saving key file info
 *     @property {function} mnemonicPhraseToggle To open mnemonic phrase pop-up
 *     @property {string} language Language code for generating mnemonic
 */
const newKeyfilePopupPropTypes = {
    toggle: PropTypes.func.isRequired,
    saveMnemonicInfo: PropTypes.func.isRequired,
    mnemonicPhraseToggle: PropTypes.func.isRequired,
    language: PropTypes.string.isRequired
}

/**
 * Pop-up displayed when user clicks "Add new keyfile" on the {@link KeysPage}.
 * Asks the user to input a password and then generates a mnemonic phrase for the new keyfile.
 */
export class NewKeyfilePopup extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            password: "",
            passwordRepeat: "",
            showError: false
        }
        this.handleChange = this.handleChange.bind(this);
        this.generateMnemonicPhrase = this.generateMnemonicPhrase.bind(this);
    }

    /**
     * Function to handle changes to the input elements: password and passwordRepeat
     * @param event - the event that triggered the onChange function.
     */
    handleChange(event) {
        this.setState({[event.target.name]: event.target.value});
    }

    /** Sends HTTP post request to generate a mnemonic phrase for the new keyfile. */
    async generateMnemonicPhrase() {
        //checks to make sure password is non-empty and the two passwords match:
        if (this.state.password !== this.state.passwordRepeat || this.state.password === "") {
            this.setState({showError: true});
        }else {
            const response = await createAndSendRequest("wallet_generateMnemonic",
                {"language": this.props.language});
            this.props.saveMnemonicInfo(
                response.data.result["mnemonicPhrase"],
                response.data.result["seed"],
                this.state.password
            );

            this.props.toggle();
            this.props.mnemonicPhraseToggle();
        }
    }

    render() {
        //initializes styling:
        let passwordStyle = "input-style";
        let repeatPasswordStyle = "input-style";
        let errorMsg = "";

        // sets warning style and warning message:
        if (this.state.showError) {
            if (this.state.password === "") {
                errorMsg = "Please fill in the missing field(s) above.";
                passwordStyle = "input-warning-style";
                repeatPasswordStyle = "input-warning-style";
            }else if (this.state.password !== this.state.passwordRepeat) {
                repeatPasswordStyle = "input-warning-style";
                errorMsg = "The passwords do not match."
            }
        }
        return (
            <div className={"new-keyfile-popup"}>
                <CloseButton style={{marginRight: "20px"}} handleClose={this.props.toggle} />
                <h1> Create a new keyfile </h1>

                <div className={"password"}>
                    <span> Enter password: </span>
                    <input className={passwordStyle} style={{width: "100%"}} type={"password"} name={"password"}
                           placeholder={"Password"} value={this.state.password} onChange={this.handleChange}/>
                </div>

                <div className={"password"}>
                    <span> Repeat password: </span>
                    <input className={repeatPasswordStyle} style={{width: "100%"}} type={"password"}
                           name={"passwordRepeat"} placeholder={"Password"} value={this.state.passwordRepeat}
                           onChange={this.handleChange}/>
                </div>

                {(this.state.showError)
                    ? <span style={{color: "red", fontSize: "12px", display: "block"}}>
                        {errorMsg}
                    </span>
                    : null
                }

                <button className={"round-blue-button"} style={{marginTop: "30px", marginLeft: "40%"}}
                        onClick={this.generateMnemonicPhrase}>
                    Create keyfile
                </button>
            </div>
        )
    }
}

NewKeyfilePopup.propTypes = newKeyfilePopupPropTypes