import React from "react";
import PropTypes from "prop-types";
import {BiPlus, BsQuestionCircle} from "react-icons/all";
import selectedSwitch from "images/switch-selected.svg";
import {SendRecipient} from "../SendRecipient";
import ReactTooltip from "react-tooltip";
import {createAndSendRequest} from "modules/apiRequests";
import {capitalize, insufficientFunds, invalidRecipients} from "modules/sharedFunctions";

/**
 * Props for {@link GenerateRawTx}
 *  @property {boolean} online True if gjallarhorn's connected to bifrost.
 *  @property {function} toggleSendOffline Toggles between send offline and online
 *  @property {function} changeStep To move on to next step in send offline process after completing this step
 *  @property {string} fromAddress The address of the currently unlocked keyfile
 *  @property {string} changeAddress
 *  @property {string} consolidationAddress
 *  @property {Array.<{address: string, amount: number}>} recipients List of recipients and amount to send.
 *  @property {string} boxType Type of asset to send: "arbit", "poly", or "asset"
 *  @property {number} currentBalance The balance of the currently opened keyfile.
 */
const generateRawTxPropTypes = {
    online: PropTypes.bool.isRequired,
    toggleSendOffline: PropTypes.func.isRequired,
    changeStep: PropTypes.func.isRequired,
    fromAddress: PropTypes.string.isRequired,
    changeAddress: PropTypes.string.isRequired,
    consolidationAddress: PropTypes.string.isRequired,
    recipients: PropTypes.arrayOf(PropTypes.shape({
        address: PropTypes.string,
        amount: PropTypes.number
    })).isRequired,
    boxType: PropTypes.oneOf(["arbit", "poly", "asset"]).isRequired,
    currentBalance: PropTypes.number.isRequired
}

/**
 * Component for generating a raw transaction.
 * This step is called "generateTx" in {@link ../SendOfflineConnected} and {@link ../SendOfflineDisconnected}.
 */
export class GenerateRawTx extends React.Component {
    constructor(props) {
        super(props);
        this.state={
            rawTx: null,
            recipients: this.props.recipients,
            showError: false,
            errorMsg: null,
            consolidationAddress: this.props.consolidationAddress,
            changeAddress: this.props.changeAddress
        }
        this.createRawTx = this.createRawTx.bind(this);
        this.addRecipient = this.addRecipient.bind(this);
        this.removeRecipient = this.removeRecipient.bind(this);
        this.editRecipient = this.editRecipient.bind(this);
        this.handleChange = this.handleChange.bind(this);
    }

    /**
     * Function to handle changes to input elements: changeAddress and consolidationAddress
     * @param event - the event that prompted the onChange function
     */
    handleChange(event) {
        this.setState({[event.target.name]: event.target.value});
    }

    /** Sends HTTP post request to create a raw transaction */
    async createRawTx() {
        //checks for invalid inputs:
        if (invalidRecipients(this.state.recipients, this.props.currentBalance)) {
            this.setState({showError: true});
            if (insufficientFunds(this.state.recipients, this.props.currentBalance)) {
                this.setState({errorMsg: "You do not have enough balance for this transaction."});
            }
        } else {
            //puts recipients in the correct format
            const recipients = this.state.recipients.map(recp => [recp.address, recp.amount])

            //sets changeAddress - defaults to the "fromAddress"
            let changeAddress = this.props.fromAddress
            if (this.props.changeAddress !== "") {
                changeAddress = this.props.changeAddress
            }

            //offline request
            let method = "wallet_createRawTransaction";
            let txType = capitalize(this.props.boxType) + "Transfer";
            let params = {
                "txType": txType,
                "propositionType": "PublicKeyCurve25519",
                "sender": [this.props.fromAddress],
                "recipients": recipients,
                "changeAddress": changeAddress,
                "consolidationAddress": this.state.consolidationAddress,
                "fee": 1,
                "data": "",
                "online": false
            }

            //online request (through bifrost)
            if (this.props.online) {
                method = "onlineWallet_createTransaction";
                params = {
                    "method": "topl_raw" + txType,
                    "params": params
                }
            }

            const response = await createAndSendRequest(method, params);
            if (response.data.error) {
                this.setState({
                    showError: true,
                    errorMsg: response.data.error.message
                });
            }else{
                this.setState({rawTx: response.data.result});
            }
        }
    }

    /**
     * Called when user clicks the "add recipient" button.
     * Adds an empty recipient to the list of recipients.
     */
    addRecipient() {
        this.setState({
            recipients: this.state.recipients.concat({
                address: "",
                amount: null
            })
        });
    }

    /**
     * Modifies a recipient in this.state with the given new info
     * @param {{address: string, amount: number}} recipientInfo - The new recipient info.
     * @param {number} index - The index of the recipient to be changed.
     */
    editRecipient(recipientInfo, index) {
        let newRecipients = this.state.recipients;
        newRecipients.splice(index, 1, recipientInfo);
        this.setState({
            recipients: newRecipients,
            errorMsg: null
        });
    }

    /**
     * Removes a recipient from this.state.recipients
     * @param {number} index - the index for the recipient to be removed.
     */
    removeRecipient(index) {
        let newRecipients = this.state.recipients;
        newRecipients.splice(index, 1);
        this.setState({recipients: newRecipients});
    }

    /**
     * Renders the change address input field
     * @returns {JSX.Element} the change address input field
     */
    renderChangeAddress() {
        return (<div className={"field"} style={{marginTop: "0px"}}>
                                    <span> Change address:
                                        <BsQuestionCircle data-tip="" data-for={"change-address"} style={{verticalAlign: "middle", marginLeft: "5px"}}/>
                                    </span>
            <input type="text" name={"changeAddress"} className={"address-box"}
                   placeholder={this.props.fromAddress} value={this.state.changeAddress}
                   style={{height: "20px"}} onChange={this.handleChange}/>
            <ReactTooltip id={"change-address"} place={"bottom"} effect={"solid"}>
                <span style={{fontWeight: "500"}}> Address to send leftover Poly's to </span>
            </ReactTooltip>
        </div>)
    }

    /**
     * Render the consolidation address input field
     * @returns {JSX.Element} the element for the consolidation address input field
     */
    renderConsolidationAddress() {
        return (<div className={"field"} style={{marginTop: "0px"}}>
            <div style={{float: "left"}}>
                <div> Consolidation address: </div>
                <div>
                    (optional)
                    <BsQuestionCircle data-tip="" data-for={"consolidation-address"}
                                      style={{verticalAlign: "middle", marginLeft: "5px"}}/>
                </div>
            </div>
            <input type="text" name={"consolidationAddress"} className={"address-box"}
                   placeholder={"Enter address"} value={this.state.consolidationAddress}
                   style={{height: "20px"}} onChange={this.handleChange}/>
            <ReactTooltip id={"consolidation-address"} place={"bottom"} effect={"solid"}>
                <span style={{fontWeight: "500"}}> Address to send leftover {this.props.boxType}'s to </span>
            </ReactTooltip>
        </div>)
    }

    render() {
        let button = <button className={"blue-square-button"} onClick={this.createRawTx}
                             style={{fontSize: '16px', fontWeight: "600", float: "none", margin: "20px 10px 20px 20px", width: "200px"}}>
            Generate TX To Export
        </button>

        //if the rawTx is defined, ready to export json file
        if (this.state.rawTx) {
            let data = "data:text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(this.state.rawTx))
            button = <button className={"blue-square-button"}
                        style={{fontSize: '14px', fontWeight: "600", float: "none", margin: "20px 10px 20px 20px"}}>
                <a href={data} download={"topl-raw-tx.json"} style={{color: "white"}}> Export JSON File </a>
            </button>
        }

        return (
            <div>
                {/* Display all of the recipient input fields */}
                <div style={{marginTop: "30px", marginBottom: "20px"}}>
                    {this.state.recipients.map((recipient, index) => {
                        return <SendRecipient addressValue={recipient.address} boxType={this.props.boxType}
                                              amount={recipient.amount} index={index} removeFunc={this.removeRecipient}
                                              showRemoveButton={this.state.recipients.length > 1}
                                              saveNewInfo={this.editRecipient} error={this.state.showError}/>})
                    }
                </div>

                <button className={"text-button"} style={{paddingLeft: "13px", paddingBottom: "20px"}} onClick={this.addRecipient}>
                    <BiPlus className={"plus-symbol"}/>
                    Add Recipient
                </button>

                {this.renderChangeAddress()}

                {/* Only render consolidation address for assets and arbits */}
                {this.props.boxType !== "poly" ? this.renderConsolidationAddress() : null}

                {/* Render fee input field */}
                <div className={"field"} style={{paddingBottom: "25px"}}>
                    <span> Fee: </span>
                    <span className={"number"}> 1 </span>
                </div>

                {/* Render send offline switch */}
                {this.props.online
                    ? <span className={"field"}>
                            Send offline
                            <img src={selectedSwitch} className="offline-switch" alt="offline-switch"
                                 onClick={this.props.toggleSendOffline}/> </span>
                    : null
                }

                <div>
                    {button}
                    {this.state.errorMsg ? <span style={{color: "red"}}> Error: {this.state.errorMsg} </span> : null}

                    {/* If online, show button for already have signed tx.
                    If offline, show button to go back to other offline options. */}
                    {this.props.online
                        ? <button className={"text-button"}
                                  onClick={() => this.props.changeStep("importSignedTx")}> Already have signed
                            tx </button>
                        : <button className={"blue-square-button"} onClick={()=>this.props.changeStep("initialPage")}
                                  style={{fontWeight: "500", fontSize: "16px", width: "170px"}}>
                            Back to all options
                        </button>}
                </div>
            </div>
        )
    }
}

GenerateRawTx.propTypes = generateRawTxPropTypes