import React from "react";
import PropTypes from "prop-types";

/**
 * Props for {@link SendRecipient}
 *  @property {string} addressValue The address of the recipient, defaulted to empty string,
 *  @property {number} amount The amount to be sent to the recipient, defaulted to null,
 *  @property {string} boxType Type of asset to send: "arbit", "poly", or "asset"
 *  @property {boolean} showRemoveButton If there are more than one recipients, then should show remove button
 *  @property {function} removeFunc Used to remove this recipient
 *  @property {function} saveNewInfo Used to save changes when user edits input fields
 *  @property {boolean} error If an error was found after the user tries to click "next"
 */
const sendRecipientPropTypes = {
    addressValue: PropTypes.string,
    amount: PropTypes.number,
    boxType: PropTypes.oneOf(["arbit", "poly", "asset"]).isRequired,
    showRemoveButton: PropTypes.bool.isRequired,
    removeFunc: PropTypes.func.isRequired,
    saveNewInfo: PropTypes.func.isRequired,
    error: PropTypes.bool.isRequired
}

const sendRecipientDefaultProps = {
    addressValue: "",
    amount: null
}

/** The component for a recipient when sending assets. Displays "Send to" and "Amount" input fields. */
export class SendRecipient extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            addressValue: this.props.addressValue,
            amount: this.props.amount,
            decimalWarning: false
        }
        this.handleChange = this.handleChange.bind(this);
    }

    /**
     * Function to handle changes to input elements: addressValue and amount
     * @param event - the event that triggers the onChange function.
     */
    handleChange(event) {
        this.setState({
            [event.target.name]: event.target.value});
        if (event.target.name === "addressValue") {
            this.props.saveNewInfo({address: event.target.value, amount: this.state.amount});
        }

        //if changing the amount: must check for decimal and must parse the value as an Int.
        else if (event.target.name === "amount") {
            if (event.target.value.includes(".")) {
                this.setState({decimalWarning: true});
            }else{
                this.setState({decimalWarning: false});
            }
            this.props.saveNewInfo({address: this.state.addressValue, amount: parseInt(event.target.value)})
        }
    }

    render() {
        let currency;

        //Set the currency code:
        if (this.props.boxType === "poly") {
            currency = "POLY"
        }else if (this.props.boxType === "arbit") {
            currency = "ARB"
        }

        //set the styles for the input boxes
        let addressStyle = "address-box";
        let amountStyle = "amount-box";

        //set warning styles and warning message based on the warning:
        let warningMsg;
        if (this.state.decimalWarning) {
            amountStyle = "amount-box-warning";
            warningMsg = "The amount must be a whole number."
        }
        if (this.props.error) {
            if (this.state.addressValue === "") {
                addressStyle = "address-box-warning";
            }
            if (this.state.amount && this.state.amount <=0 ) {
                warningMsg = "The amount must be greater than 0.";
                amountStyle = "amount-box-warning";
            }
            if (!this.state.amount) {
                warningMsg = "This field is required."
                amountStyle = "amount-box-warning";
            }
        }

        return(
            <div style={{borderBottomStyle: "solid", borderColor: "white"}}>

                {/* "Send to" component: */}
                <div className={"field"} style={{marginTop: "0px"}}>
                    <span> Send to: </span>
                    <input type="text" name={"addressValue"} className={addressStyle} placeholder={"Enter address"} value={this.state.addressValue}
                           onChange={this.handleChange}/>
                    {(addressStyle === "address-box-warning") ? <span className={"error-msg"}> This field is required. </span> : null}
                </div>

                {/* "Amount" component: */}
                <div className={"field"} style={{marginTop: "10px", marginBottom: "10px"}}>
                    <span> Amount: </span>
                    <input type="number" name={"amount"} className={amountStyle} placeholder={"0"} value={this.state.amount}
                           onChange={this.handleChange}/>
                    <span className={"currency"}> {currency} </span>

                    {/* Display remove button - only if there are multiple recipients: */}
                    {(this.props.showRemoveButton)
                        ? <button className={"text-button"} style={{float: "right", marginRight: "10px", marginTop: "30px"}}
                                onClick={this.props.removeFunc}>
                            Remove Recipient
                        </button>
                        : null
                    }

                    {/* Display warning message: */}
                    {(amountStyle === "amount-box-warning") ? <span className={"error-msg"}> {warningMsg} </span> : null}

                </div>
            </div>
        );
    }
}

SendRecipient.propTypes = sendRecipientPropTypes
SendRecipient.defaultProps = sendRecipientDefaultProps